import store from '@/store'
import { lodash } from '@/utils/helper'
import { useTimestamp } from '@/utils/CommonJs.js'
import { getTrackEventReport, getUserTrackEventReport, getEventReportVisit } from '@/api/modules/distinguish.js'

/**
 * 渠道管理埋点
 * 游客状态打点
 * @param {*} sign 标识 1:页面每次打开 2:下载按钮点击
 */
let pointTimer = null,
  pointflag = false
export async function channelBurialPoint(sign = 1) {
  if (pointflag) return
  pointflag = true

  // 是否是游客
  const isTourist = store.getters['base/isTourist']
  if (!isTourist) return

  const { adJustKey, pkgName = '' } = store?.state?.base || {}
  // 设备id
  const deviceid = store.getters['base/getDeviceID']
  // 渠道码
  const agentid = localStorage.getItem('agentid') || ''
  // 渠道名称
  const { trackerName = '' } = lodash.isObject(adJustKey) ? adJustKey : {}
  const ajtrackerName = trackerName
  // source_type
  const source_type = localStorage.getItem('source_type') || ''

  // 参数
  const params = {
    deviceid,
    agentid,
    ajtrackerName,
    pkgName,
    source_type,
    events: { [sign]: 1 }
  }
  await getTrackEventReport(params).catch(e => e)

  // 节流 2 秒
  pointTimer = setTimeout(() => {
    pointflag = false
    if (pointTimer) {
      clearTimeout(pointTimer)
      pointTimer = null
    }
  }, 2 * 1000)
}

/**
 * AJ打点
 * commonConfig 新增 track_event_report_conf 字段，是否开启打点【0关 1开】
 *      ----> 11:用户头像下拉选单 12:底部按钮、下载/客服按钮 13:分类栏 14:左侧边栏
 * 聚合统计后每10秒推送一次信息
 * 游客/用户都需要上报
 */
const AJ_DOTTING_MD = {
  /**
   * 用户头像下拉框【11】 ---> 例: 11_0_1
   */
  USER_DOWN_VIP: [11, 1], // vip
  USER_DOWN_COPY: [11, 2], // 用户id复制按钮
  USER_DOWN_MINE: [11, 3], // 用户中心
  USER_DOWN_MUSIC: [11, 4], // 音乐开关（无）
  USER_DOWN_SOUND: [11, 5], // 音效开关
  USER_DOWN_MAIL: [11, 6], // 站内信
  USER_DOWN_SERVICE: [11, 7], // 客服
  USER_DOWN_LOGOUT: [11, 8], // 登出
  USER_DOWN_GIFTCODE: [11, 9], // 礼包码
  /**
   * 底部按钮+下载/客服按钮【12】 ---> 例: 12_0_1
   */
  OTHER_BTN_BOTTOM_MENU: [12, 1], // 大厅 - 底部
  OTHER_BTN_BOTTOM_REFER: [12, 2], // 推广 - 底部
  OTHER_BTN_BOTTOM_ACTIVITY: [12, 3], // 活动 - 底部
  OTHER_BTN_BOTTOM_MAIL: [12, 4], // 站内信 - 底部
  OTHER_BTN_BOTTOM_MINE: [12, 5], // 用户中心 - 底部
  OTHER_BTN_FLOAT_SERVICE: [12, 6], // 客服-浮动
  OTHER_BTN_FLOAT_DOWN: [12, 7], // 下载-浮动
  OTHER_BTN_TOP_DOWN: [12, 8], // 下载-顶部（无）
  OTHER_BTN_BOOK_IOS: [12, 9], // ios-书签
  OTHER_BTN_BOTTOM_SPORT: [12, 10], // 体育入口-底部
  /**
   * 分类栏【13】 ---> 例: 13_分组id_1
   */
  CLASSIFICATION_TAB: [13, 1], // 标签
  CLASSIFICATION_ALL: [13, 2], // 查看更多
  CLASSIFICATION_MORE: [13, 3], // 下一页
  /**
   * 左侧栏【14】 ---> 例: 14_0_1
   */
  LEFT_HOME: [14, 1], // 大厅-左侧(Home)
  LEFT_VIP: [14, 2], // VIP界面-左侧
  LEFT_REFER: [14, 3], // 推广-左侧
  LEFT_BET: [14, 4], // 洗码
  LEFT_LUCKY: [14, 5], // 转盘
  LEFT_ACTIVITY_DETAIL: [14, 6], // 活动详情-左侧
  LEFT_ACTIVITY: [14, 7], // 全部活动-左侧
  LEFT_LANG: [14, 8] // 语言切换
}
export const AJ_DOTTING = {
  ajTimer: null,
  aJTimeNum: 10, // 每10秒推送一次信息
  isAjDot: false, // 是否开启 AJ 打点
  /**
   * 初始化判断是否开启 AJ 打点
   */
  init: function () {
    const { systemConfig: { track_event_report_conf = {} } = {} } = store.state.base
    if (!lodash.isObject(track_event_report_conf)) return

    const objData = Object.values(track_event_report_conf)
    if (objData.includes(1)) {
      this.isAjDot = true
      this.getAjTimer()
    }
  },
  /**
   * 开始 AJ 打点计时器
   */
  getAjTimer: function () {
    if (this.ajTimer) {
      clearTimeout(this.ajTimer)
      this.ajTimer = null
    }

    const { user: { uid = 0 } = {} } = store.state.base || {}
    this.ajTimer = setTimeout(() => {
      const ajData = JSON.parse(localStorage.getItem('AJ_CLICK_DOTTING') || '{}')
      if (ajData[uid] && Object.keys(ajData[uid]).length > 0) {
        this.aJTimeNum--
        if (this.aJTimeNum <= 0) {
          this.aJTimeNum = 10
          this.getAjReporting()
        }
      }
      this.getAjTimer()
    }, 1 * 1000)
  },
  /**
   * 储存打点数据
   * @param {*} key 对应的key
   * @param {*} group_id 分类栏分类组对应的 id
   */
  getAjdata: function (key = '', group_id = 0) {
    if (!key || !this.isAjDot) return

    const { user: { uid = 0 } = {}, systemConfig: { track_event_report_conf = {} } = {} } = store.state.base || {}
    const events_arr = AJ_DOTTING_MD[key] || []
    if (!events_arr.length) return

    const [first, last] = events_arr
    // 判断当前按钮是否开启打点
    if (track_event_report_conf[first] != 1) return
    const events_key = `${first}_${group_id}_${last}`

    const ajData = JSON.parse(localStorage.getItem('AJ_CLICK_DOTTING') || '{}')
    if (ajData[uid]) {
      let aj_uid = ajData[uid] || {}
      aj_uid[events_key] = aj_uid[events_key] ? Number(aj_uid[events_key]) + 1 : 1
    } else {
      ajData[uid] = { [events_key]: 1 }
    }

    localStorage.setItem('AJ_CLICK_DOTTING', JSON.stringify(ajData))
  },
  /**
   * 上报
   * 缓存里有数据才上报
   * 每一次上报，数据都要重置
   */
  getAjReporting: async function () {
    const { user: { token = '', uid = 0 } = {}, languageType = '' } = store.state.base || {}

    const ajData = JSON.parse(localStorage.getItem('AJ_CLICK_DOTTING') || '{}')
    const events = ajData[uid] || {}
    if (!Object.keys(events).length) return

    // 数据重置
    this.aJTimeNum = 10
    ajData[uid] = {}
    localStorage.setItem('AJ_CLICK_DOTTING', JSON.stringify(ajData))

    const params = { token, language: languageType, events }
    const res = await getUserTrackEventReport(params).catch(e => e)
  }
}

/**
 * 访问量打点
 * 每天第一次访问大厅时，上报访问量，且每天只上报一次(不区分游客)
 */
export async function visitDotting() {
  const visit_dot = JSON.parse(localStorage.getItem('CC_VISIT_DOT') || '{}')
  const currentDate = useTimestamp(new Date(), 'yyyy-MM-dd')
  if (visit_dot[currentDate]) return

  await getEventReportVisit({ event: 'visit_homepage' }).catch(e => e)
  localStorage.setItem('CC_VISIT_DOT', JSON.stringify({ ...visit_dot, [currentDate]: 1 }))
}
