import { initializeApp } from 'firebase/app'
import { getAnalytics, logEvent } from 'firebase/analytics'
import {
  FIREBASE_APIKEY,
  FIREBASE_AUTH_DOMAIN,
  FIREBASE_PROJECT_ID,
  FIREBASE_STORAGE_BUCKET,
  FIREBASE_MESSAGING_SENDER_ID,
  FIREBASE_APPID,
  FIREBASE_MEASUREMENT_ID
} from '@/api/setting'
import { EVENT_KEY } from '../eventDefind'

class FirebaseAds {
  constructor() {
    this.analytics = null
    this.config = {
      apiKey: FIREBASE_APIKEY,
      authDomain: FIREBASE_AUTH_DOMAIN,
      projectId: FIREBASE_PROJECT_ID,
      storageBucket: FIREBASE_STORAGE_BUCKET,
      messagingSenderId: FIREBASE_MESSAGING_SENDER_ID,
      appId: FIREBASE_APPID,
      measurementId: FIREBASE_MEASUREMENT_ID
    }
    this.eventMap = {
      [EVENT_KEY.registration]: 'CompleteRegistration', // 注册
      [EVENT_KEY.login]: 'Login', // 登录
      [EVENT_KEY.addToCart]: 'AddToCart', // 支付拉起
      [EVENT_KEY.firstPayment]: 'FirstRecharge', // 首次充值
      [EVENT_KEY.completePayment]: 'CompletePayment' //支付成功和支付金额
    }
  }
  init() {
    if (!this.config.appId || this.analytics) return
    try {
      const firebaseConfig = {
        apiKey: FIREBASE_APIKEY,
        authDomain: FIREBASE_AUTH_DOMAIN,
        projectId: FIREBASE_PROJECT_ID,
        storageBucket: FIREBASE_STORAGE_BUCKET,
        messagingSenderId: FIREBASE_MESSAGING_SENDER_ID,
        appId: FIREBASE_APPID,
        measurementId: FIREBASE_MEASUREMENT_ID
      }
      const app = initializeApp(firebaseConfig)
      this.analytics = getAnalytics(app)
    } catch (error) {
      console.error(error)
    }
  }

  logEvent(eventKey, data) {
    if (!this.analytics) return
    const eventName = this.eventMap[eventKey]
    console.warn('firebase ads logEvent', eventName, data || '')
    eventName && logEvent(this.analytics, eventName, data)
  }
}

export default FirebaseAds
