/**
 * @desc: 存储体育赛事数据
 * */
import { make } from 'vuex-pathify'
import { gameUp, gameDown } from '@/api/modules/game'

const state = {
  betbyMoney: 0, // 体育赛事金额
  betbyData: {} // 体育赛事数据
}

const getters = {}
const mutations = make.mutations(state)
const actions = {
  // 上分
  upperDivision: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      const { code, data, msg } = await gameUp(params).catch(e => e)
      if (code != 0) {
        $pop.message().show({ content: msg })
        return reject('cancal')
      }

      const { other_data = {} } = data
      const { end_gold = 0 } = other_data

      commit('SET_BETBY_MONEY', end_gold)
      commit('SET_BETBY_DATA', other_data)

      resolve(other_data)
    })
  },
  // 下分
  lowerDivision: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      const { code, data, msg } = await gameDown(params).catch(e => e)
      if (code != 0) {
        $pop.message().show({ content: msg })
        return reject('cancel')
      }

      resolve()
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
