// 获取API访问失败备用域名
export const getBackupUrl = params => {
  return fetch(params)
    .then(response => {
      return response.json()
    })
    .then(text => {
      return text
    })
    .catch(error => {
      console.error('Error fetching data: ', error)
    })
}

let _timeout = null
// 获取验证多域名
export const createRequestWithTimeout = (url, options, timeout = 10000) => {
  return new Promise((resolve, reject) => {
    clearTimeout(_timeout)
    _timeout = null
    const abort = new AbortController()
    options = options || {}
    if (options.signal) {
      options.signal.addEventListener('abort', () => {
        abort.abort()
      })
    }
    options.signal = abort.signal
    _timeout = setTimeout(() => {
      reject(new Error('Request timeout'))
      abort.abort()
    }, timeout)

    fetch(url, options)
      .then(response => {
        return response.json()
      })
      .then(data => {
        return resolve(data)
      })
      .catch(error => {
        reject(error)
      })
  })
}

