import loadIcon from '@/assets/icons.js'
import { VUE_APP_ARRAY } from '@/api/setting.js'

// 收藏操作：触发后，保存到本地localstorage中
export const collect = (item, { $store: store }) => {
  let list = store.state.source.collectGames
  let gameItme = JSON.parse(JSON.stringify(item))
  // 如果是子游戏就对比list的gameid和game.gameid是否相等
  if (gameItme.gameid) {
    if (list.some(g => g.gameid == gameItme.gameid)) {
      let removedList = list.filter(g => g.gameid !== gameItme.gameid)
      store.commit('source/SET_COLLECT_GAMES', removedList)
    } else {
      gameItme.isFully = false
      list = [...list, gameItme]
      store.commit('source/SET_COLLECT_GAMES', list)
    }
  } else if (gameItme.has_child) {
    if (list.some(g => g.id == gameItme.id)) {
      let removedList = list.filter(g => g.id !== gameItme.id)
      store.commit('source/SET_COLLECT_GAMES', removedList)
    } else {
      gameItme.isFully = false
      list = [...list, gameItme]
      store.commit('source/SET_COLLECT_GAMES', list)
    }
  } else {
    if (list.some(g => g.gid == gameItme.gid)) {
      let removedList = list.filter(g => g.gid !== gameItme.gid)
      store.commit('source/SET_COLLECT_GAMES', removedList)
    } else {
      gameItme.isFully = false
      list = [...list, gameItme]
      store.commit('source/SET_COLLECT_GAMES', list)
    }
  }
}
export const collectIcon = (key, isStyle, item, { $store: store }) => {
  let icon = key
  let list = store.state.source.collectGames
  if (item.gameid) {
    if (list.some(g => g.gameid == item.gameid)) icon = `${key}_active`
  } else if (item.has_child) {
    if (list.some(g => g.id == item.id)) icon = `${key}_active`
  } else {
    if (list.some(g => g.gid == item.gid)) icon = `${key}_active`
  }
  return loadIcon(icon, isStyle)
}

let vueAppArray = VUE_APP_ARRAY
// 域名切换
export const getEnvConfig = {
  // 调用次数
  index: 1,
  // 域名数组长度
  urlLength: vueAppArray.length,
  // 副站点域名
  subSiteUrl: '',
  // 获取 切换的域名
  baseURL: function () {
    return this.subSiteUrl || vueAppArray[this.index - 1]
  },
  set: function () {
    this.index++
  },
  // 设置 副站点域名
  setSubSiteUrl: function (url = '') {
    if (!url) return

    this.subSiteUrl = url
  },
  add: function (list) {
    // 新增备用域名，但是需要出去重复数据
    vueAppArray = [...new Set([...vueAppArray, ...list])]
    this.urlLength = vueAppArray.length
  },
  // 重置
  reset: function () {
    this.index = 1
    this.urlLength = vueAppArray.length
    this.subSiteUrl = ''
  }
}
