import store from '@/store'

export const hasLossLimit = vm => {
  const is_loss_limit = store.state.responsible?.lossLimitItem?.is_loss_limit || 0
  if (is_loss_limit == 1) {
    // 达到每日限额
    vm.$pop.message().show({ content: vm.$t('UI.Resp_Loss_Limit_Tips_3') })
    return true
  } else if (is_loss_limit == 2) {
    // 达到每周限额
    vm.$pop.message().show({ content: vm.$t('UI.Resp_Loss_Limit_Tips_4') })
    return true
  } else if (is_loss_limit == 3) {
    // 达到每月限额
    vm.$pop.message().show({ content: vm.$t('UI.Resp_Loss_Limit_Tips_5') })
    return true
  }
  return false
}
