const isArray = value => Object.prototype.toString.call(value) === '[object Array]'
const isObject = value => Object.prototype.toString.call(value) === '[object Object]'

function objStr(obj = {}) {
  let str = ''
  for (const key in obj) {
    if (str) {
      str += `&${key}=${obj[key]}`
    } else {
      str += `${key}=${obj[key]}`
    }
  }

  return str
}

/**
 * 高阶函数，封装某个api方法，返回一个新方法，此新方法具有缓存请求的功能
 * @param {function} fn
 * @param {string} fnName
 * @return {function} new api fn
 */
export default function withCache(fn, fnName = '') {
  let __cache__ = {}
  fnName = fnName || fn.name
  const outputFn = function (...args) {
    const KEY = `AJAX_API_MEMERY_CACHE_${fnName}_`
    const key =
      KEY +
      args
        .map(arg => {
          if (isArray(arg) || isObject(arg)) {
            return objStr(arg)
          } else {
            return arg
          }
        })
        .join('&')
    const cache = __cache__[key]
    if (cache) return Promise.resolve(JSON.parse(JSON.stringify(cache)))
    return fn.apply(null, args).then(res => {
      if (res.code == 0) {
        __cache__[key] = res
      }
      return res
    })
  }
  outputFn.clearCache = () => {
    __cache__ = {}
  }
  return outputFn
}
