/**
 * 用于实现 BI 观察，行为识别接口
 */

import request from '@/api/http.js'
import {
  api_postDownload,
  api_track_event_report,
  api_user_track_event_report,
  api_event_report_visit
} from './httpServerAPI'

// apk下载埋点
export const postDownloadRecord = async params => {
  return await request.post(api_postDownload, params)
}

// 渠道管理埋点
export const getTrackEventReport = async params => {
  return await request.post(api_track_event_report, params)
}

// AJ打点埋点
export const getUserTrackEventReport = async params => {
  return await request.post(api_user_track_event_report, params)
}

// 访问量打点
export const getEventReportVisit = async params => {
  return await request.post(api_event_report_visit, params)
}
