import { make } from 'vuex-pathify'
import SocketServer from '@/server/SocketServer'
import { MsgEventDefine } from '@/server/enums/MsgEventDefine'
import NoticeHandler from '@/server/handler/notice-handler'

// 主题加载器
const state = {
  net: null
}

const getters = {}

const mutations = make.mutations(state)

const actions = {
  createWs: async ({ rootState, state, commit }) => {
    return new Promise(resolve => {
      if (state.net) state.net.close()
      const wsUrl = rootState.base.wsUrl
      const net = new SocketServer(wsUrl, rootState.base.user.token)
      net.connect()
      commit('SET_NET', net)
      resolve()
    })
  },

  // 添加消息监听器
  addListener({ state }, app) {
    NoticeHandler['$app'] = app
    NoticeHandler['fn']['$app'] = app
    const net = state.net
    net.addHandler(MsgEventDefine.EVENT_LOGIN, NoticeHandler['EVENT_LOGIN'].bind(NoticeHandler))
    net.addHandler(MsgEventDefine.EVENT_NOTIFY, NoticeHandler['EVENT_NOTIFY'].bind(NoticeHandler))
    net.addHandler(MsgEventDefine.EVENT_LOGOUT, NoticeHandler['EVENT_LOGOUT'].bind(NoticeHandler))
    net.addHandler(MsgEventDefine.EVENT_UPDATE_GOLD, NoticeHandler['EVENT_UPDATE_GOLD'].bind(NoticeHandler))
    net.addHandler(MsgEventDefine.EVENT_OUT_CONNECTED, NoticeHandler['EVENT_OUT_CONNECTED'].bind(NoticeHandler))
    // todo
    // if (code == 10005) return NoticeHandler['EVENT_ACCOUNT_SEAL'](data)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
