import { EVENT_KEY } from '../eventDefind'

class FacebookAds {
  constructor() {
    this.injectScriptTml = `
			!function(f,b,e,v,n,t,s)
			{if(f.fbq)return;n=f.fbq=function(){n.callMethod?
			n.callMethod.apply(n,arguments):n.queue.push(arguments)};
			if(!f._fbq)f._fbq=n;n.push=n;n.loaded=!0;n.version='2.0';
			n.queue=[];t=b.createElement(e);t.async=!0;
			t.src=v;s=b.getElementsByTagName(e)[0];
			s.parentNode.insertBefore(t,s)}(window, document,'script',
			'https://connect.facebook.net/en_US/fbevents.js');
			fbq('init', '#{PIXEL_ID}');
			fbq('track', 'PageView');
        `
    this.eventMap = {
      [EVENT_KEY.pageView]: 'PageView', // 浏览页面
      [EVENT_KEY.registration]: 'CompleteRegistration', // 注册
      [EVENT_KEY.login]: 'Login', // 登录
      [EVENT_KEY.addToCart]: 'AddToCart', // 支付拉起
      [EVENT_KEY.firstPayment]: 'AddToWishlist', // 首次充值
      [EVENT_KEY.completePayment]: 'Purchase' // 充值完成
    }
    this.pixelid = null
  }

  init(pixelid) {
    if (!pixelid) return
    this.pixelid = pixelid
    const script = document.createElement('script')
    script.id = 'PIXEL_FBQ'
    script.innerHTML = this.injectScriptTml.replaceAll('#{PIXEL_ID}', this.pixelid)
    document.head.appendChild(script)
  }

  logEvent(eventKey, data) {
    if (!window.fbq) return
    const eventName = this.eventMap[eventKey]
    console.warn('facebook ads logEvent', eventName, data || '')
    eventName && fbq('track', eventName, data)
  }
}

export default FacebookAds
