/**
 * @desc 获取当前展示平台类型
 */
export function getPlatform() {
  var ua = navigator.userAgent.toLowerCase()
  const isPhone = () =>
    ua.match(/iphone os/i) == 'iphone os' || ua.match(/iph os/i) == 'iph os' || ua.match(/ipad/i) == 'ipad'
  const isAndroid = () =>
    ua.match(/android/i) == 'android' || ua.match(/adr/i) == 'adr' || ua.match(/android/i) == 'mi pad'
  if (isPhone()) return 'ios'
  if (isAndroid()) return 'android'
  if (!(isPhone() && isAndroid())) return 'Windows'
}
/**
 * @desc 生成UUID
 */
function createUUID() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    var r = (Math.random() * 16) | 0,
      v = c == 'x' ? r : (r & 0x3) | 0x8
    return v.toString(16)
  })
}

function createID() {
  return 'xxxxx'.replace(/[xy]/g, function (c) {
    var r = (Math.random() * 16) | 0,
      v = c == 'x' ? r : (r & 0x3) | 0x8
    return v.toString(16)
  })
}

/**
 * @desc 重置store中的游戏列表中游戏是否收藏，添加collect状态
 * 数据描述：游戏类型分为自研游戏和第三方游戏，第三方游戏的数据存储在game_list下的sub_game_list
 * 开发：仅需要对game_list下的数据判断是否存在子游戏列表，若存在，则对子游戏列表
 */
export function setGameCollectState(list) {
  const setGameList = () => {}
  return list.map(item => setGameList(item))
}
/**
 * @desc 获取游戏卡片的宽高数值
 * @branch app 每行卡片大小跟随container容器的布局进行计算，每行3张游戏卡片，每张卡片间隔20px
 * @branch pc  每行卡片大小跟随container容器的布局进行计算，每行6张游戏卡片，每张卡片间隔20px
 */
export function getGameBox(isApp, size) {
  let w = 0
  let margin = 8
  let padding = 8
  let spacing = 11
  if (isApp) w = (size - margin * 2 - padding * 2 - spacing * 2) / 3
  // if (!isApp) w = (size - 24 - 12 * 5 - 12) / 6;
  return {
    width: `${w}px`,
    height: `${w * 1.35}px`
  }
}
/**
 * @desc 大厅首页的recent卡片盒子大小计算,兼容app, pc
 */
export function getRecentGameBox(isApp, size) {
  let box = {}
  if (isApp) box.width = box.height = (size - 12 * 2) / 5
  if (!isApp) box.width = box.height = (size - 12 * 2) / 8
  box.width = `${box.width}px`
  box.height = `${box.height}px`
  return box
}

export function isEmail(v) {
  // eslint-disable-next-line no-useless-escape
  let reg = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
  return reg.test(v)
}

export function isNull(v) {
  if (v === null) {
    return true
  } else {
    return false
  }
}

/**
 * @desc 对象，数组的判断工具,后期根据项目判断是否需要引入lodash.js,否则会导致包体积新增
 */
export const lodash = {
  isBlankObj(obj) {
    return JSON.stringify(obj) === '{}'
  },
  isBlank(obj) {
    return (typeof obj == 'string' && obj == '') || obj == undefined
  },
  isMoney(value) {
    return !this.isBlank(value) && !isNaN(value) && parseFloat(value) > 0
  },
  isJSON(obj) {
    return (
      typeof obj == 'object' && Object.prototype.toString.call(obj).toLowerCase() == '[object object]' && !obj.length
    )
  },
  isBlankJSON(obj) {
    return this.isJSON(obj) && Object.keys(obj).length == 0
  },
  isArray(obj) {
    return typeof obj == 'object' && Object.prototype.toString.call(obj).toLowerCase() == '[object array]'
  },
  isBlankArray(obj) {
    return this.isArray(obj) && obj.length > 0
  },
  parseJSON2String(obj, delimiter) {
    return Object.keys(obj)
      .map(v => `${v}=${obj[v]}`)
      .join(delimiter)
  },
  json2String(v, delimiter) {
    return this.isJSON(v) ? this.parseJSON2String(v, delimiter) : ''
  },
  isObject(obj) {
    return Object.prototype.toString.call(obj) == '[object Object]'
  },
  isJSONString(data) {
    if (typeof data == undefined) return false
    try {
      JSON.parse(data)
    } catch (e) {
      return false
    }
    return true
  }
}
/**
 * @desc 存取款的cpf数值设置
 */
export function createCPF(v) {
  if (lodash.isBlank(v)) return ''
  let arr = v.split('')
  let part1 = arr.slice(0, 3).join('')
  let str = ''
  if (!arr.includes('.') && !arr.includes('-')) {
    str = `${part1}`
    if (arr.length > 3 && !arr.includes('.') && !arr.includes('-')) {
      let part2 = arr.slice(3, 6).join('')
      str += `.${part2}`
    }
    if (arr.length > 6 && !arr.includes('.') && !arr.includes('-')) {
      let part3 = arr.slice(6, 9).join('')
      str += `.${part3}`
    }
    if (arr.length > 9 && !arr.includes('.') && !arr.includes('-')) {
      let part4 = arr.slice(9).join('')
      str += `-${part4}`
    }
    return str
  } else {
    return v
  }
}
/**
 * @desc 存取款的cpf数值反设置
 */
export function againstCPF(v) {
  if (lodash.isBlank(v)) return ''
  let a = v.split('.').join('')
  let b = a.split('-').join('')
  return b
}

/**
 * 初始化项目加载页面时，对路由的处理
 */
export const getRouter = route => {
  let url = ''
  if (route.name == null) {
    url = location.hash.replace('#/', '').toLocaleLowerCase()
  } else {
    url = route.name
  }
  return url
}

export const lowerCase = value => {
  return typeof value == 'string' && value.toLocaleLowerCase()
}
//JS 数字，金额 用逗号 隔开（数字格式化）
export const getFormatterMoney = (value, size) => {
  size = size > 0 && size <= 20 ? size : 2
  value = parseFloat((value + '').replace(/[^\d\.-]/g, '')).toFixed(size) + ''
  let l = value.split('.')[0].split('').reverse(),
    r = value.split('.')[1]
  let t = ''
  for (let i = 0; i < l.length; i++) {
    t += l[i] + ((i + 1) % 3 == 0 && i + 1 != l.length ? ',' : '')
  }
  return t.split('').reverse().join('') + '.' + r
}

function getQueryValue(queryName) {
  var query = decodeURI(window.location.search.substring(1))
  var vars = query.split('&')
  for (var i = 0; i < vars.length; i++) {
    var pair = vars[i].split('=')
    if (pair[0] == queryName) {
      return pair[1]
    }
  }
  return null
}

let timeout = null

/**
 * 防抖原理：一定时间内，只有最后一次操作，再过wait毫秒后才执行函数
 *
 * @param {Function} func 要执行的回调函数
 * @param {Number} wait 延时的时间
 * @param {Boolean} immediate 是否立即执行
 * @return null
 */
export const debounce = (func, wait = 500, immediate = false) => {
  // 清除定时器
  if (timeout !== null) clearTimeout(timeout)
  // 立即执行，此类情况一般用不到
  if (immediate) {
    var callNow = !timeout
    timeout = setTimeout(function () {
      timeout = null
    }, wait)
    if (callNow) typeof func === 'function' && func()
  } else {
    // 设置定时器，当最后一次操作后，timeout不会再被清除，所以在延时wait毫秒后执行func回调方法
    timeout = setTimeout(function () {
      typeof func === 'function' && func()
    }, wait)
  }
}

let timer, flag
/**
 * 节流原理：在一定时间内，只能触发一次
 *
 * @param {Function} func 要执行的回调函数
 * @param {Number} wait 延时的时间
 * @param {Boolean} immediate 是否立即执行
 * @return null
 */
export const throttle = (func, wait = 500, immediate = true) => {
  if (immediate) {
    if (!flag) {
      flag = true
      // 如果是立即执行，则在wait毫秒内开始时执行
      typeof func === 'function' && func()
      timer = setTimeout(() => {
        flag = false
      }, wait)
    }
  } else {
    if (!flag) {
      flag = true
      // 如果是非立即执行，则在wait毫秒内的结束处执行
      timer = setTimeout(() => {
        flag = false
        typeof func === 'function' && func()
      }, wait)
    }
  }
}

/**
 * 数字保留两位小数百分比(不含百分号)
 */
export function twoDigit(v, size) {
  if (!size) size = 2
  return isNaN(v) ? 0.0 : parseFloat(v).toFixed(size)
}
/**
 * 保留小数，不四舍五入
 * value 数值
 * size 保留小数点的位数
 */
export function formatNumber(value = 0, size = 2) {
  const str = String(value)
  if (isNaN(Number(str))) return '0.00'

  const arr = str.split('.')
  if (arr.length > 1) {
    const [first = 0, last = 0] = arr
    const newLast = last.slice(0, size)
    return `${first}.${newLast}`
  } else {
    return Number(arr[0]).toFixed(size)
  }
}

export function formatterNumber(v) {
  return isNaN(parseFloat(v)) ? 0 : parseFloat(v)
}

/**
 * 正则替换
 * @param {*} stringStr 替换前的字符串
 * @param {*} oldStr 需要替换的字符串
 * @param {*} newStr 需要替换成的字符串
 */
export function replaceSrt(stringStr = '', oldStr = '', newStr) {
  if (!stringStr || !oldStr || !newStr) return stringStr

  return stringStr.replace(eval(`/${oldStr}/g`), newStr)
}

// 判断图片是否加载成功
export function isImgLoad(url = '') {
  return new Promise((resolve, reject) => {
    if (!url) resolve(false)

    const img = new Image()
    img.src = url
    // 加载成功
    img.onload = () => resolve(true)
    // 加载失败
    img.onerror = () => resolve(false)
  })
}

// 时间
export const datetime = {
  strpad: function (str) {
    str = String(str)
    if (str.length == 1) {
      return '0' + str
    } else {
      return str
    }
  },
  /**
   * 获取指定时间
   * @param {*} n 指定时间天数
   */
  getTime: function (n) {
    //上周的开始时间
    // console.log(getTime(7));
    //上周的结束时间
    // console.log(getTime(1));
    //本周的开始时间
    // console.log(getTime(0));
    //本周的结束时间
    // console.log(getTime(-6));
    //下周的开始时间
    // console.log(getTime(-7));
    //下周结束时间
    // console.log(getTime(-13));

    let now = new Date()
    let year = now.getFullYear()
    let month = now.getMonth() + 1 // 因为月份是从0开始的,所以获取这个月的月份数要加1才行
    let date = now.getDate()
    let day = now.getDay()

    //判断是否为周日,如果不是的话,就让今天的day-1(例如星期二就是2-1)
    if (day !== 0) {
      n = n + (day - 1)
    } else {
      n = -1
    }
    if (day) {
      //这个判断是为了解决跨年的问题
      if (month > 1) {
        month = month
      }
      //这个判断是为了解决跨年的问题,月份是从0开始的
      else {
        year = year - 1
        month = 12
      }
    }

    now.setDate(now.getDate() - n)
    year = now.getFullYear()
    month = now.getMonth() + 1
    date = now.getDate()

    return year + '-' + this.strpad(month) + '-' + this.strpad(date)
  },
  // 日期比对大小,返回日期
  compareDate: function (beginDate, endDate, pattern = 1) {
    // eslint-disable-next-line no-useless-escape
    let d1 = beginDate ? new Date(beginDate.replace(/\-/g, '/')) : new Date()
    // eslint-disable-next-line no-useless-escape
    let d2 = endDate ? new Date(endDate.replace(/\-/g, '/')) : new Date()

    d1 = getFormatDate({ date: d1, pattern })
    d2 = getFormatDate({ date: d2, pattern })

    if (d1 > d2) return [d2, d1]
    if (d1 < d2) return [d1, d2]

    return [d1, d2]
  },
  /**
   * 给出时间戳，返回日期
   * @param {*} timestamp 时间戳
   */
  getDate__useTimestamp: function (timestamp) {
    const date = new Date(timestamp)
    const y = date.getFullYear()
    let m = date.getMonth() + 1
    m = this.strpad(m)
    let d = date.getDate()
    d = this.strpad(d)

    return y + '-' + m + '-' + d
  },
  // 获取今天
  gettoday: function () {
    const date = new Date()
    const y = date.getFullYear()
    let m = date.getMonth() + 1
    m = this.strpad(m)
    let d = date.getDate()
    d = this.strpad(d)

    return y + '-' + m + '-' + d
  },
  // 获取昨天
  getYesterday: function () {
    let date = new Date()
    date.setTime(date.getTime() - 24 * 60 * 60 * 1000)
    const y = date.getFullYear()
    let m = date.getMonth() + 1
    m = this.strpad(m)
    let d = date.getDate()
    d = this.strpad(d)

    return y + '-' + m + '-' + d
  },
  // 获取上一周
  getUpperWeekDate: function () {
    return [this.getTime(7), this.getTime(1)]
  },
  // 获取当前周
  getCurrentWeekDate: function () {
    return [this.getTime(0), this.getTime(-6)]
  },
  // 获取下一周
  getLowerWeekDate: function () {
    return [this.getTime(-7), this.getTime(-13)]
  },
  // 获取下个月第一天
  newMonth: function () {
    const now = new Date()
    let year = now.getFullYear()
    let month = now.getMonth() + 1 // 因为月份是从0开始的,所以获取这个月的月份数要加1才行

    //这个判断是为了解决跨年的问题
    if (month == 12) {
      year = year + 1
      month = 1
    } else {
      month = Number(month) + 1
    }

    return year + '-' + this.strpad(month) + '-' + '01'
  },
  // 获取下个月
  nextMonth: function () {
    const date = this.gettoday()
    const arr = date.split('-')
    // 获取当前日期的年份
    let year = arr[0]
    // 获取当前日期的月份
    const month = arr[1]

    let nextMonth = parseInt(month) + 1
    if (nextMonth == 13) {
      year = parseInt(year) + 1
      nextMonth = 1
    }
    if (nextMonth < 10) nextMonth = `0${nextMonth}`

    // 获取下个月的天数
    const days = new Date(year, nextMonth, 0).getDate()

    return [`${year}-${nextMonth}-01`, `${year}-${nextMonth}-${days}`]
  },
  // 获取上个月
  prevMonth: function () {
    const date = this.gettoday()
    const arr = date.split('-')
    // 获取当前日期的年份
    let year = arr[0]
    // 获取当前日期的月份
    const month = arr[1]

    let prevMonth = parseInt(month) - 1
    if (prevMonth <= 0) {
      year = parseInt(year) - 1
      prevMonth = 12
    }
    if (prevMonth < 10) prevMonth = `0${prevMonth}`

    // 获取下个月的天数
    const days = new Date(year, prevMonth, 0).getDate()

    return [`${year}-${prevMonth}-01`, `${year}-${prevMonth}-${days}`]
  },
  /**
   * 计算两个时间相差的时间（天数 + 时分秒）
   * @param {*} timeone 时间1
   * @param {*} timeone 时间2
   */
  getDifferTime: function (timeone = new Date(), timetow = new Date()) {
    const dayTime = 60 * 60 * 24 * 1000
    const hTime = 60 * 60 * 1000
    const mTime = 60 * 1000
    const sTime = 1000

    let time1 = timeone
    let time2 = timetow
    // 判断是否是已经格式化后的时间
    // eslint-disable-next-line no-useless-escape
    if (String(time1).includes('-')) time1 = time1.replace(/\-/g, '/')
    // eslint-disable-next-line no-useless-escape
    if (String(time2).includes('-')) time2 = time2.replace(/\-/g, '/')
    // 转为时间戳
    time1 = new Date(time1).getTime()
    time2 = new Date(time2).getTime()
    // 相差的时间/秒
    const differTime = (time1 > time2 ? time1 - time2 : time2 - time1) || 0
    // 相差的时间/天
    const day = Math.floor(differTime / dayTime)
    // 相差的时间/时
    const hh = Math.floor((differTime % dayTime) / hTime)
    // 相差的时间/分
    let mm = differTime - day * dayTime - hTime * hh
    mm = Math.floor(mm / mTime)
    // 相差的时间/秒
    let ss = differTime - day * dayTime - hTime * hh - mTime * mm
    ss = Math.floor(ss / sTime)

    return {
      day: this.strpad(day),
      hh: this.strpad(hh),
      mm: this.strpad(mm),
      ss: this.strpad(ss)
    }
  },
  /**
   * 获取当前月份的天数
   * @param {*} y 年份
   * @param {*} m 月份
   */
  mGetDate: function (y, m) {
    const date = new Date()
    const year = y ? y : date.getFullYear()
    const month = m ? m : date.getMonth() + 1
    const d = new Date(year, month, 0)

    return d.getDate()
  },
  /**
   * 秒数格式化
   * @param {*} second 秒
   */
  getFormatTime: function (second = 0) {
    const seco = Number(second)
    let text = ''
    // 小时
    let hour = parseInt(seco / 3600)
    if (hour > 0) {
      let hourText = hour > 1 ? $t('UI.Responsible_Hours') : $t('UI.Responsible_Hour')
      hour = `${hour} ${hourText}`
      text = text + hour
    }
    // 分钟
    let minute = parseInt((seco % 3600) / 60)
    // 秒
    const s = parseInt(seco - hour * 3600 - minute * 60)
    minute = s > 0 && !minute ? 1 : minute

    if (minute > 0) {
      minute = `${minute} ${$t('UI.Responsible_Min')}`
      text = text ? `${text} ${minute}` : minute
    }

    return text
  },
  /**
   * 秒数格式化 2
   * @param {*} second 秒
   */
  getFormatTimeTwo: function (second = 0) {
    const seco = Number(second)
    // 小时
    const hour = parseInt(seco / 3600)
    // 分钟
    const minute = parseInt((seco % 3600) / 60)
    // 秒
    const s = parseInt(seco - hour * 3600 - minute * 60)

    return `${this.strpad(hour)}:${this.strpad(minute)}:${this.strpad(s)}`
  },
  /**
   * 秒数格式化 3
   * @param {*} second 秒
   */
  getFormatTimeThree: function (seconds = 0) {
    // 计算天数
    const days = Math.floor(seconds / (24 * 60 * 60))
    // 计算剩余的小时数
    seconds %= 24 * 60 * 60
    const hours = Math.floor(seconds / (60 * 60))
    // 计算剩余的分钟数
    seconds %= 60 * 60
    const minutes = Math.floor(seconds / 60)
    // 计算剩余的秒数
    const remainingSeconds = seconds % 60

    return {
      days: days,
      hours: hours,
      minutes: minutes,
      seconds: remainingSeconds
    }
  },
  // 指定时间格式
  formatDateTime: function (date, format) {
    const o = {
      yyyy: date.getFullYear(), // 年份
      'M+': date.getMonth() + 1, // 月份
      'd+': date.getDate(), // 日
      'h+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12, // 小时
      'H+': date.getHours(), // 小时
      'm+': date.getMinutes(), // 分
      's+': date.getSeconds(), // 秒
      'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
      S: date.getMilliseconds(), // 毫秒
      a: date.getHours() < 12 ? 'AM' : 'PM', // 上午/下午
      A: date.getHours() < 12 ? 'AM' : 'PM' // AM/PM
    }
    if (/(y+)/.test(format)) {
      format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (let k in o) {
      if (new RegExp('(' + k + ')').test(format)) {
        format = format.replace(RegExp.$1, RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length))
      }
    }
    return format
  },
  // 1.首先获取本地时间和服务器时间的时间间隔
  dateDiff: function (date1, date2) {
    return new Date(date2) - new Date(date1)
  },
  /**
   * 计算两个日期间相差的天数
   */
  getDateDiff: function (startDate, endDate) {
    var startTime = new Date(Date.parse(startDate.replace(/-/g, '/'))).getTime()
    var endTime = new Date(Date.parse(endDate.replace(/-/g, '/'))).getTime()
    var dates = Math.abs(startTime - endTime) / (1000 * 60 * 60 * 24)
    return dates
  }
}

//判断图片是否存在
export function checkImgExists(imgurl = '') {
  var ImgObj = new Image() //判断图片是否存在
  ImgObj.src = imgurl
  //存在图片
  if (ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
    return true
  } else {
    return false
  }
}

export function getUrlParams(params) {
  return Object.keys(params)
    .map(v => `${[v]}=${params[v]}`)
    .join('&')
}

/**
 * 解决 富文本中字体大小和颜色转为html标签
 * @param obj
 * @param rest
 * @returns {*}
 */
export function getEeplace(test = '') {
  // eslint-disable-next-line no-useless-escape
  let test1Data = test.replace(/\&lt;/g, '<')
  // eslint-disable-next-line no-useless-escape
  test1Data = test1Data.replace(/\&gt;/g, '>')
  test1Data = test1Data.replace(/\\&quot;/g, "'")
  // eslint-disable-next-line no-useless-escape
  test1Data = test1Data.replace(/\<color=#([^>]+)\>\<size=([^>]+)>/g, (match, p1, p2) => {
    // eslint-disable-next-line no-useless-escape
    return `\<span style="color:#` + p1 + `;font-size:` + setSizeFun(p2) + `;"\>`
  })
  // eslint-disable-next-line no-useless-escape
  test1Data = test1Data.replace(/\<\/size\>\<\/color\>/g, '</span>')
  // eslint-disable-next-line no-useless-escape
  test1Data = test1Data.replace(/\<size=([^>]+)\>\<color=#([^>]+)\>/g, (match, p1, p2) => {
    // eslint-disable-next-line no-useless-escape
    return `\<span style="color:#` + p2 + `;font-size:` + setSizeFun(p1) + `;"\>`
  })
  // eslint-disable-next-line no-useless-escape
  test1Data = test1Data.replace(/\<\/color\>\<\/size\>/g, '</span>')
  return test1Data
}
// 富文本中字体大小转换
export const setSizeFun = size => {
  let resultSize = ''
  if (size == 24) {
    resultSize = 1.0 + 'em'
  }
  if (size == 26) {
    resultSize = 1.2 + 'em'
  }
  if (size == 28) {
    resultSize = 1.4 + 'em'
  }
  return resultSize
}

/**
 * 解决Vue Template模板中无法使用可选链的问题
 * @param obj
 * @param rest
 * @returns {*}
 */
export const optionalChaining = (obj, ...rest) => {
  let tmp = obj
  for (let key in rest) {
    let name = rest[key]
    tmp = tmp?.[name]
  }
  return tmp || ''
}

/**
 * TAG: function - 缓存数据
 * TAG: function - 压缩数据
 * TAG: function - 解压数据
 */
export const Storage = {
  log(val) {
    let info = LZUTF8.decompress(val, {
      inputEncoding: 'Base64',
      outputEncoding: 'String'
    })
    return JSON.parse(info)
  },
  set(key, value) {
    if (lodash.isBlankJSON(value)) {
      localStorage.setItem(key, '')
      return
    }
    let json = JSON.stringify(value)
    let t = LZUTF8.compress(json, {
      outputEncoding: 'Base64'
    })
    localStorage.setItem(key, t)
  },
  get(key) {
    let k = localStorage.getItem(key)
    let info = {}
    if (localStorage.getItem(key) == null) {
      return info
    } else {
      info = LZUTF8.decompress(k, {
        inputEncoding: 'Base64',
        outputEncoding: 'String'
      })
      if (info) info = JSON.parse(info)
    }
    return info
  },
  remove(key) {
    if (!key) {
      localStorage.clear()
    }
    let exludes = Object.keys(localStorage).filter(v => !key.split(',').includes(v))
    if (key) {
      exludes.forEach(name => {
        localStorage.removeItem(name)
      })
    }
  }
}

