/**
 * @Date: 2023-03-06 13:45:58
 * @description: 调用原生安卓方法
 * @param {String} methodName
 * @param {Object} dataJson
 * @return {*}
 */
export const callJava = (methodName, dataJson) => {
  if (!window.android) {
    return console.warn('Failed to load android platform', `methodName: ${methodName}`, `dataJson: ${dataJson}`)
  }
  console.warn('callJava', `methodName: ${methodName}`, `dataJson: ${dataJson}`)
  if (!dataJson) {
    dataJson = {}
  }
  let strJson = JSON.stringify(dataJson)
  return window.android.callJava(methodName, strJson)
}

/**
 * NOTE: 被动触发事件，客户端的桥接，Android/ios会主动触发该事件
 * name: 事件名
 * 		getDeviceIdByIDFA			设备id
 * 		getDeviceId					设备id
 * 		onBackKey					安卓返回按钮
 * 		loginFaceBookSuccess		facebook登录
 * 		onRequestPermissionsResult	相机权限申请
 *
 * callback: 回调
 */
export const callScriptMethod = (name = '', callback = () => {}) => {
  if (!window.android) return callback()

  const callScript = (method = '', val = '{}') => {
    if (method == name) callback(JSON.parse(val))
  }

  if (!(window?.cc?.mg?.native_class?.callScript && window?.android?.callScript)) {
    window.cc = { mg: { native_class: { callScript } } }
    window.android.callScript = callScript
  } else {
    window.cc.mg.native_class.callScript = callScript
    window.android.callScript = callScript
  }
}

/**
 * @desc 生成设备ID
 */
export function createDeviceId() {
  let deviceid = null
  // 真机apk上获取设备id
  if (window.android) {
    deviceid = callJava('getDeviceId')
    localStorage.setItem('deviceid', deviceid)
    return deviceid
  }
  deviceid = localStorage.getItem('deviceid')
  if (deviceid == '' || deviceid == null) {
    let platform = getPlatform()
    deviceid = `${platform}_${createUUID()}`
    localStorage.setItem('deviceid', deviceid)
  }
  return deviceid
}

/**
 * 动态加载js文件
 * @param {*} urls
 */
export function addJSLink(urls = []) {
  const js = urls.filter(f => f.includes('.js'))
  const css = urls.filter(f => f.includes('.css'))

  const newUrlJs = js.map(async url => await importScript(url))
  const newUrlCss = css.map(async url => await importStyle(url))

  return Promise.all([...newUrlJs, ...newUrlCss])
}

// 中划线、下划线隔开的变量转为小驼峰、横杠
export function camelCase(str = '') {
  return str.replace(/[_-][a-zA-z]/g, str => str.substr(-1).toUpperCase())
}

export function isAndroidStandalone() {
  return typeof android != 'undefined'
}

export function AdjustlogEvent(systemConfig, type, value) {
  if (!isAndroidStandalone()) return
  const { aj_event_config } = systemConfig
  const aj_event_configData =
    Array.isArray(aj_event_config) && aj_event_config.length ? aj_event_config.find(item => item.type == type) : null
  if (!aj_event_configData) return

  const typetoken = aj_event_configData.key || ''
  const tCurrencyType = aj_event_configData.currency || ''
  const tObjData = value || 0
  const jsonData = { typetoken, tCurrencyType, tObjData }
  callJava('adjustlogEvent', jsonData, new Date().getTime())
}

export function setMetaThemeColor(addClass) {
  const metaThemeColor = document.querySelector('meta[name="theme-color"]')
  try {
    if (metaThemeColor) {
      // 修改meta标签的主题颜色
      switch (addClass) {
        // 默认色值具栏背景色
        case 'body-scrol':
          metaThemeColor.setAttribute('content', _style.top_nav_bg)
          break
        // 弹窗默认色值具栏背景色
        case 'body-remove-Scroll':
          metaThemeColor.setAttribute('content', _style.mask_color)
          break
        // 子页面和充值页面关闭色值变回来
        case 'body-top-navigation-Scroll':
          metaThemeColor.setAttribute('content', _style.top_nav_bg)
          break
        // 子页面比如个人中心，钱包，站内信, 顶部工具栏背景色
        case 'body-project-bg-remove-Scroll':
          metaThemeColor.setAttribute('content', _style.project_bg)
          break
        // 充值提现弹窗打开, 顶部工具栏背景色
        case 'body-top-navigation-remove-Scroll':
          metaThemeColor.setAttribute('content', _style.card_bg)
          break
        // 默认色值具栏背景色
        default:
          metaThemeColor.setAttribute('content', _style.top_nav_bg)
          break
      }
    }
  } catch (error) {
    console.log(error)
  }
}

export function unScroll(addClass = 'body-remove-Scroll') {
  var bodyEl = document.body
  //计算滚动条宽度 (滚动条的宽度不是都为17px的，算一下当前的比较精确)
  const outer = document.createElement('div')
  const inner = document.createElement('div')
  outer.style.overflow = 'scroll'
  document.body.appendChild(outer)
  outer.appendChild(inner)
  const scrollbarWidth = outer.offsetWidth - inner.offsetWidth
  document.body.removeChild(outer)
  bodyEl.style['padding-right'] = scrollbarWidth
  while (bodyEl.classList.length > 0) {
    bodyEl.classList.remove(bodyEl.classList.item(0))
  }
  bodyEl.classList.add(addClass)
  setMetaThemeColor(addClass)
}

export function removeUnScroll(addClass = 'body-scrol') {
  var bodyEl = document.body
  bodyEl.style['padding-right'] = '0px'
  while (bodyEl.classList.length > 0) {
    bodyEl.classList.remove(bodyEl.classList.item(0))
  }
  bodyEl.classList.add(addClass)
  setMetaThemeColor(addClass)
}

/**
 * 获取url 参数
 * @param {string} url
 * @returns {Object}
 */
export function getQueryObject(url) {
  url = url == null ? window.location.href : url
  const search = url.substring(url.lastIndexOf('?') + 1)
  const obj = {}
  const reg = /([^?&=]+)=([^?&=]*)/g
  search.replace(reg, (rs, $1, $2) => {
    const name = decodeURIComponent($1)
    let val = decodeURIComponent($2)
    val = String(val)
    obj[name] = val
    return rs
  })
  return obj
}

/**
 * base64 转换 str
 * @param {Sting} val
 * @returns
 */
export function b64_to_str(val) {
  let ret = LZUTF8.decompress(val, {
    inputEncoding: 'Base64',
    outputEncoding: 'String'
  })
  return ret
}

/* 小数点处理
 * @param {*} str 数据源
 * @param {*} num 保留几位小数
 */
export function getPoint(str = '', num = 2) {
  const strNum = String(str)

  if (!strNum) return (0.0).toFixed(num)

  if (isNaN(strNum)) {
    return (0.0).toFixed(num)
  } else {
    return Number(strNum).toFixed(num)
  }
}

/* 是否存在小数点，如果存在小数点，则保留num位小数，如果是整数，则不保留小数
 * @param {*} str 数据源
 * @param {*} num 保留几位小数
 */

export function getToFixed(str = '', num = 2) {
  if (str.toString().includes('.')) {
    return Number(str).toFixed(num)
  }
  return str
}
/**
 * 转为千位符
 * @param {*} num 数字或字符串
 * @param {*} isBoolean 是否转为千位符
 */
export function formatKilobit(num = 0, isBoolean = false) {
  if (!num) return '0.00'

  const str = String(num)
  let parts = str.split('.')

  parts[0] = isBoolean ? parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',') : parts[0]
  parts[1] = parts[1] ? (parts[1].length > 1 ? parts[1].substring(0, 2) : `${parts[1]}0`) : '00'

  return parts.join('.')
}

/**
 * 转换时分秒
 * @param {*} countdown 时间戳
 */
export function formattedTime(countdown) {
  const minutes = Math.floor(countdown / 60)
  const seconds = countdown % 60
  return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
}

/**
 * 纯数字转换成千分符字符串
 * @param str 数字字符串
 * @returns
 */

export function formatNumberWithCommas(num) {
  const parts = String(num).split('.')
  parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',')
  return parts.join('.')
}

/**
 * 千分符字符串转换成纯数字
 * @param str 数字字符串
 * @returns
 */
export function formatCommasNumber(str = '') {
  str = String(str)
  return str.replace(/,/g, '')
}

/**
 * 千分符字符串转换成纯数字
 * @param isState 是否开启  0关闭， 1开启
 * @param type 纯数字转换成千分符字符串 或者  千分符字符串转换成纯数字
 * @param str 转换参数
 * @returns
 */
export function isFormatMillageAndNumber(str, isType, isState) {
  if (!isState || isState == '0') {
    return str
  }
  if (isType) {
    return formatNumberWithCommas(str)
  } else {
    return formatCommasNumber(str)
  }
}

/**
 *  使用正则表达式匹配千分符
 * @param character 参数
 * @returns
 */
export function isComma(character) {
  const commaRegex = /[,]/
  return commaRegex.test(character)
}

/**
 * 注册，修改密码校验
 */
export function isPwdCheck(val = '') {
  return /^[a-zA-Z0-9!ñ@#$%^&*]{6,16}$/.test(val)
}

/**
 * 字符串首字母大写
 * @param {string} str 需要转换的字符串
 * @returns {string}
 * */
export function capitalizeFirstLetter(str) {
  return str
    .toLowerCase()
    .split(/\s+/)
    .map(function (item, index) {
      return item.slice(0, 1).toUpperCase() + item.slice(1)
    })
    .join(' ')
}

/**
 * 将数组按照指定的长度进行分割。
 * @param {Array} array - 要分割的原始数组。
 * @param {number} chunkSize - 每个子数组的长度。
 * @returns {Array<Array>} - 包含分割后子数组的数组。
 */
export function splitArrayIntoChunks(array, chunkSize) {
  let result = []
  for (let i = 0; i < array.length; i += chunkSize) {
    result.push(array.slice(i, i + chunkSize))
  }
  return result
}

// 过滤emoji表情和特殊符号
export function filterEmoji(str) {
  // eslint-disable-next-line no-control-regex
  let pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？↵\r\n]", 'g')
  let regEmoji = /(\ud83c[\udf00-\udfff])|(\ud83d[\udc00-\ude4f\ude80-\udeff])|[\u2600-\u2B55]/g
  str = str.replace(pattern, '')
  str = str.replace(regEmoji, '')
  return str
}
window.filterEmoji = filterEmoji

export function isPhone(str = '') {
  if (!str) return false
  return !/^[0-9]\d{9,10}$/.test(str)
}
//获取最高z-index
export const getMaxZIndex = () => {
  const elements = document.querySelectorAll('*')
  let maxZIndex = 0
  elements.forEach(element => {
    const computedStyle = window.getComputedStyle(element)
    const zIndex = parseInt(computedStyle.zIndex)
    if (!isNaN(zIndex) && zIndex > maxZIndex) {
      maxZIndex = zIndex
    }
  })
  return maxZIndex + 1
}

