export function getALLAgentid(str, key) {
  let result = []
  if (null !== str && undefined !== str) {
    const reg = /([^?&=]+)=([^?&=]*)/g
    str.replace(reg, function (rs, $1, $2) {
      const name = decodeURIComponent($1)
      let val = decodeURIComponent($2)
      val = String(val)
      if (name === key) {
        result.push(val)
      }
      return rs
    })
  }
  return result
}

// cookie 设置/获取
export const cookieInfo = {
  getCookie: function (cname) {
    const name = cname + '='
    const ca = document.cookie.split(';')
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i]
      while (c.charAt(0) == ' ') {
        c = c.substring(1)
      }
      if (c.indexOf(name) == 0) {
        let d = c.substring(name.length, c.length)
        return JSON.parse(decodeURIComponent(d))
      }
    }
    return ''
  },
  setCookie: function (cname, cvalue, exdays = 0) {
    cvalue = encodeURIComponent(JSON.stringify(cvalue))
    if (exdays > 0) {
      const d = new Date().getTime() + exdays * 24 * 3600 * 1000 + 8 * 3600 * 1000
      const expires = 'expires=' + new Date(d).toUTCString()
      document.cookie = cname + '=' + cvalue + ';' + expires + ';'
    } else {
      document.cookie = cname + '=' + cvalue + ';' + ';'
    }
  },
  deleteCookie: function (name) {
    let date = new Date()
    date.setTime(date.getTime() - 1)
    const delValue = this.getCookie(name)
    if (delValue) {
      document.cookie = name + '=' + delValue + ';expires=' + date.toGMTString()
    }
  }
}

// 深拷贝
export const deepClone = (obj, map = new WeakMap()) => {
  // 判断基本数据类型，同时注意null是object类型
  if (typeof obj !== 'object' || obj === null) {
    return obj
  }
  if (obj instanceof RegExp) {
    return new RegExp(obj)
  }
  if (obj instanceof Date) {
    return new Date(obj)
  }
  if (map.has(obj)) return map.get(obj) // 避免循环引用
  let newObj = Array.isArray(obj) ? [] : {}
  map.set(obj, newObj)
  for (const key in obj) {
    if (Object.hasOwnProperty.call(obj, key)) {
      newObj[key] = deepClone(obj[key], map)
    }
  }
  return newObj
}

