import './preload.js' // 处理页面加载时预处理的业务
import Vue from 'vue'
import createRouter from '@/router/index.js'
import { createVuetify } from '@/vuetify/index.js'
import store from '@/store/index.js'
import initGlobal from './global.js'
import { initAds } from './server/ads/index.js'
import process from 'process/browser'
window.process = process

//自定义插件或者工具链
import i18n from '@/utils/i18n'

const loadApp = () => import(/* webpackChunkName: "layout" */ '@/App')
//1.注册框架中，包含了对主题的修改
const vuetify = createVuetify(Vue)
//？2. 结合路由做处理
const router = createRouter(store)
window.globalRouter = router

initGlobal({ Vue, store, router, vuetify })

// 初始化打点服务
initAds()

// 初始化UI
loadApp().then(component => {
  let app = new Vue({
    router,
    store,
    vuetify,
    i18n,
    render: h => h(component.default)
  }).$mount('#app')
  window.$vm = app
})
