import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import language from './modules/language.js'
import assets from './modules/assets.js'
import layout from './modules/layout.js'
import source from './modules/source.js'
import base from './modules/base.js'
import modal from './modules/modal.js'
import gamews from './modules/gamews.js'
import dialog from './modules/dialog.js'
import switchState from './modules/switchState.js'
import sport from './modules/sport.js'
import responsible from './modules/responsible.js'
import game from './modules/game.js'
import stepLoader from './modules/stepLoader.js'
import common from './modules/common.js'

const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  reducer: state => {
    // 做持久化
    return {
      ...state,
      base: {
        dataVersion: state.base.dataVersion,
        systemConfig: state.base.systemConfig,
        user: state.base.user,
        userHistory: state.base.userHistory,
        languageType: state.base.languageType,
        adJustKey: state.base.adJustKey,
        volume: state.base.volume
      },
      layout: {},
      modal: {},
      gamews: {},
      source: {
        rechargeUser: state.source.rechargeUser,
        // withdrawUser: state.source.withdrawUser,
        // gameCollection: state.source.gameCollection,
        // furyInfo: state.source.furyInfo,
        // countryGameLogo: state.source.countryGameLogo,
        collectGames: state.source.collectGames,
        // languageList: state.source.languageList,
        // gamelist: state.source.gamelist,   // 加上这个 localStorage缓存超出限制
        recent: state.source.recent,
        reportLastTime: state.source.reportLastTime,
        secondRechargeList: state.source.secondRechargeList
      },
      dialog: {},
      sport: {},
      responsible: {},
      game: {},
      stepLoader: {},
      common: {}
    }
  }
})

Vue.use(Vuex)
const store = new Vuex.Store({
  modules: {
    language,
    assets,
    layout,
    source,
    base,
    modal,
    switchState,
    gamews,
    dialog,
    sport,
    responsible,
    game,
    stepLoader,
    common
  },
  state: {},
  mutations: {},
  actions: {},
  plugins: [vuexLocal.plugin]
})

export default store
