import Vue from 'vue'
import VueI18n from 'VueI18n'
import { isProduct, csv_data_arr } from '@/api/setting.js'
// https://kazupon.github.io/vue-i18n/zh/guide/formatting.html#%E5%85%B7%E5%90%8D%E6%A0%BC%E5%BC%8F

Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: '' // 设置默认语言
})

/**
 * 加载多语言
 * @param {*} lang 语言
 */
// 开发环境设置多语言
let isLoadSuccess = false
const loadMessagesDev = async (lang = '') => {
  return new Promise((resolve, reject) => {
    if (isLoadSuccess) return resolve()
    class Translate {
      constructor() {
        this.messages = {}
        this.loadedLanguages = []
      }
      parseSheet(data) {
        data.forEach((row, rowIndex) => {
          let key
          row.forEach((col, colIndex) => {
            // 第一行，文件名
            if (rowIndex === 0 && colIndex !== 0) {
              if (!(col in this.messages)) {
                this.messages[col] = {}
                this.loadedLanguages.push(col)
              }
            }

            if (rowIndex >= 1) {
              if (colIndex === 0) {
                key = col
              } else {
                const lang = this.loadedLanguages[colIndex - 1]
                if (!lang) return
                this.messages[lang][key] = col
              }
            }
          })
        })

        return this
      }
    }
    function CSVToArray(data) {
      // 提取对应语言
      const first = [
        'key',
        ...Object.keys(data[0])
          .filter(val => data[0][val] !== null && val !== 'key')
          .map(val => val.trim())
      ]
      let list = data.map(item => {
        let t = []
        // 提取首列：key
        t.push(item.key)
        // 提取对应语言下的value
        t = [
          ...t,
          ...Object.keys(item)
            .filter(val => item[val] !== null && val !== 'key')
            .map(val => item[val])
        ]
        return t
      })
      list = [first, ...list]

      return Promise.resolve(list)
    }
    CSVToArray(csv_data_arr)
      .then(sheets => {
        const translate = new Translate().parseSheet(sheets)
        for (const key in translate.messages) {
          if (Object.hasOwnProperty.call(translate.messages, key)) {
            const message = translate.messages[key]
            i18n.setLocaleMessage(key, message)
          }
        }
      })
      .then(r => {
        isLoadSuccess = true
        resolve()
      })
  })
}
// 生产环境设置多语言
const loadMessagesProd = async (lang = '') => {
  return new Promise((resolve, reject) => {
    const { languagePath = '' } = JSON.parse(process.env.VUE_APP_PATH)
    // eslint-disable-next-line no-undef
    fetch(`${languagePath}/${lang}.json?v=${VUE_APP_VTIME}`)
      .then(res => {
        return res.json()
      })
      .then(messages => {
        i18n.mergeLocaleMessage(lang, messages)
        resolve()
      })
      .catch(e => {
        reject()
      })
  })
}

/**
 * 更新多语言
 * @param {*} lang 语言
 */
let isShake = false
i18n.setI18nLanguage = (lang = '') => {
  if (isShake) return
  isShake = true

  const setLang = isProduct ? loadMessagesProd : loadMessagesDev
  setLang(lang)
    .then(r => {
      i18n.locale = lang
      document.querySelector('html').setAttribute('lang', lang)
      isShake = false
    })
    .catch(e => {
      isShake = false
      console.error('Multi language loading failed')
    })
}

export const $t = key => {
  return i18n.t(key)
}
export default i18n
