import Vue from 'vue'
import store from '@/store'
import { getResour } from '@/utils/loadServer.js'

let vConsoleNum = 5

/**
 * 第二梯队加载的组件/资源
 * @param {*} sign part:必要部分 all:全部
 */
export function initComponent(sign = 'all') {
  let loadComp = []

  if (sign == 'part' || sign == 'all') {
    loadComp = [
      // 登录注册
      {
        name: 'login-modal',
        load: _ => {
          const { loginModalName = '' } = store.state?.stepLoader || {}
          if (loginModalName) return
          import('@/components/login').then(result => {
            Vue.component('login-modal', result.default)
            store.commit('stepLoader/SET_LOGIN_MODAL_NAME', 'login-modal')
          })
        }
      }
    ]
  }
  if (sign == 'all') {
    // 充值提现组件
    let depositWithdraw = store.getters['stepLoader/globalModules']
    depositWithdraw = depositWithdraw
      .map(comp => {
        if (comp == 'deposit-detail') {
          return {
            name: 'deposit-detail',
            load: _ => {
              const { depositDetailName = '' } = store.state?.stepLoader || {}
              if (depositDetailName) return
              import('@views/deposit/index.vue').then(result => {
                Vue.component('deposit-detail', result.default)
                store.commit('stepLoader/SET_DEPOSIT_DETAIL_NAME', 'deposit-detail')
              })
            }
          }
        }
        if (comp == 'withdraw-detail') {
          return {
            name: 'withdraw-detail',
            load: _ => {
              const { withdrawDetailName = '' } = store.state?.stepLoader || {}
              if (withdrawDetailName) return
              import('@views/withdraw/index.vue').then(result => {
                Vue.component('withdraw-detail', result.default)
                store.commit('stepLoader/SET_WITHDRAW_DETAIL_NAME', 'withdraw-detail')
              })
            }
          }
        }
        return null
      })
      .filter(f => f)

    loadComp = [
      ...loadComp,
      ...depositWithdraw,
      // mdi图标
      {
        name: 'mdi-css',
        load: _ => {
          const { mdiCssName = '' } = store.state?.stepLoader || {}
          if (mdiCssName) return
          import('@mdi/font/css/materialdesignicons.css').then(result => {
            store.commit('stepLoader/SET_MDI_CSS_NAME', 'mdi-css')
          })
        }
      },
      // 加载galacean
      {
        name: 'galacean',
        load: _ => {
          const { libsPath = '' } = JSON.parse(process.env.VUE_APP_PATH)
          getResour('galacean', [`${libsPath}/ge_v1.6.5.js`])
        }
      },
      // 加载pixi
      {
        name: 'pixi',
        load: _ => {
          const { libsPath = '' } = JSON.parse(process.env.VUE_APP_PATH)
          getResour('pixi', [`${libsPath}/pixi.min.js`, `${libsPath}/pixi-spine.js`])
        }
      },
      // 动态加载字体
      {
        name: 'fonts',
        load: _ => {
          const { fontName = '' } = store.state?.stepLoader || {}
          if (fontName) return

          const { fontPath = '', fontGamePath = '' } = JSON.parse(process.env.VUE_APP_PATH)
          const { fontFilesList = [], gameFontFilesList = [] } =
            Object.prototype.toString.call(VUE_APP_FONT_FAMILY_NAME) == '[object Object]'
              ? VUE_APP_FONT_FAMILY_NAME
              : {}

          const fontList = [
            ...fontFilesList.map(font => {
              return { ...font, fontUrl: `${fontPath}${font.fontUrl}` }
            }),
            ...gameFontFilesList.map(font => {
              return { ...font, fontUrl: `${fontGamePath}${font.fontUrl}` }
            })
          ]
          if (typeof FontFace != undefined) {
            fontList.forEach(async item => {
              const { fontFamily = '', fontUrl = '', display = '', style = '', weight = 0 } = item
              let fontParams = {}
              if (display) fontParams.display = display
              if (style) fontParams.style = style
              if (weight) fontParams.weight = weight

              const font = new FontFace(fontFamily, `url(${fontUrl})`, fontParams)
              await font.load()
              document.fonts.add(font)
            })
          } else {
            let fontHtml = ''
            fontList.forEach(async item => {
              const { fontFamily = '', fontUrl = '', display = '', style = '', weight = 0 } = item
              fontHtml += `@font-face {
						${fontFamily ? 'font-family:' + fontFamily + ';' : ''}
						${fontUrl ? 'src: url(' + fontUrl + ') format("opentype");' : ''}
						${weight ? 'font-weight:' + weight + ';' : ''}
						${style ? 'font-style:' + style + ';' : ''}
						${display ? 'font-display:' + display + ';' : ''}
					}`
            })

            const styles = document.createElement('style')
            styles.innerHTML = fontHtml
            document.head.appendChild(styles)
          }

          store.commit('stepLoader/SET_FONT_NAME', 'font-name')
        }
      },
      // 加载vconsole
      {
        name: 'vconsole',
        load: _ => {
          const { consoleName = '' } = store.state?.stepLoader || {}
          if (consoleName) return

          const { systemConfig = {} } = store.state?.base || {}
          const { is_debug = false } = systemConfig

          if (is_debug) {
            const isVconsole = () => {
              setTimeout(() => {
                if (window.VConsole) {
                  store.commit('stepLoader/SET_CONSOLE_NAME', 'console-name')
                  new window.VConsole()
                  return
                }

                if (vConsoleNum > 0) {
                  vConsoleNum--
                  isVconsole()
                }
              }, 1000)
            }

            getResour('vconsole', ['https://cdn.jsdelivr.net/npm/vconsole@latest/dist/vconsole.min.js'], r => {
              if (r) isVconsole()
            })
          }
        }
      }
    ]
  }

  loadComp.forEach(c => c.load())
}
