import { make } from 'vuex-pathify'
import { getGameBox } from '@/utils/helper'

// 主题加载器
const state = {
  isApp: true,
  containerBox: {},
  currentRouter: '', //保存当前路由地址
  topNavigationHeight: [70, 56] //pc 60-86
}

const getters = {
  getGameCardBox(state) {
    return {
      width: getGameBox(state.isApp, state.containerBox.width).width,
      height: getGameBox(state.isApp, state.containerBox.width).height
    }
  }
}

const mutations = make.mutations(state)

const actions = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
