import axios from 'axios'
import store from '@/store'
import { timeout, VUE_APP_REQUEST_ERROR } from '@/api/setting.js'
import { getUrlParams } from '@/utils/helper'
import { catchTrack } from '@/utils/EventTrackManager'
import { getEnvConfig } from '@/utils/collect.js'
import PublishSubscribe from 'publish-subscribe-js'

const request = axios.create({ timeout })

request.interceptors.request.use(
  config => {
    let headers = {}
    let httpReg = /http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w- .\/?%&=]*)?/
    if (!httpReg.test(config.url)) config.baseURL = getEnvConfig.baseURL()
    headers['Content-Type'] = 'application/x-www-form-urlencoded'
    config.headers = Object.assign({}, config.headers, headers)
    catchTrack.catchHttpEvent(config.url)
    if (!store.state.base.user.token) config.data = Object.assign({}, config.data, { token: '' })
    if (store.state.base.user.token == '') config.data.token = store.state.base.user.token
    return config
  },
  error => {
    return error
  }
)
const RequestEnums = {
  TOKENINVALID: 1001, // token 失效
  TIMEOUT: 10000, // 请求超时 request timeout
  FAIL: 500, // 服务器异常 server error
  LOGINTIMEOUT: 401, // 登录超时 login timeout
  UNAUTHORIZED: 400,
  NotIMPLEMENTED: 501,
  SUCCESS: 200 // 请求成功 request successfully
}
//  500  501

// 请求到结果的拦截处理
request.interceptors.response.use(
  response => {
    let { data = {}, status, headers = {} } = response
    data = typeof data != 'object' ? { code: 0, data: null, msg: 'error' } : data

    const { code = 1001, msg = '' } = data
    // token 失效 && 项目启动成功
    if (code == RequestEnums.TOKENINVALID && store.state.base.isFiring) {
      // 清除弹窗序列
      PublishSubscribe.publish(store.state?.dialog?.DUALOG_MANAGER_CLEAR)
      // 退出游服
      if (store?.state?.gamews?.net?.closews) store?.state?.gamews?.net?.closews()
      // 清除历史用户信息
      store.state.base.userHistory = []
      // 清除用户信息
      store.commit('base/SET_USER', {})
      // message 弹窗
      window.$pop.message().show({ content: msg })
      // 返回首页
      if (window.globalRouter) window.globalRouter.push({ name: 'menu' })
      // 设备号游客登录
      store.dispatch('base/createVisitorAccount', () => {
        // 唤起登录弹窗
        store.dispatch('modal/setLoginRegistShow')
        // 隐藏充值提现的弹窗显隐
        store.dispatch('modal/setDepositWithdrawShow', { show: false })
        // 隐藏邮箱显隐
        store.dispatch('modal/setEmailState', false)

        // 重新获取邀请活动信息,游客状态清除邀请信息
        let activityInvite = store.state.source.activityInvite
        activityInvite.activity_user_task = null
        store.commit('source/SET_ACTIVITY_INVITE', activityInvite)
      })
    }

    return data
  },
  error => {
    if (error && error.response) {
      console.error(error + ': interface is error')
      let { data = {}, status, headers = {} } = error.response
      // 判断不是200 和401
      if (![RequestEnums.SUCCESS, RequestEnums.LOGINTIMEOUT].includes(status)) {
        console.log('system is error!')
      }
      if (status == RequestEnums.FAIL) {
        // return Promise.reject(data)
        return Promise.reject(VUE_APP_REQUEST_ERROR)
      }
      if ([RequestEnums.LOGINTIMEOUT].includes(status)) {
        console.log('relogin!')
      }
      window.$pop.loading().hide()
      // return Promise.reject(error)
      return Promise.reject(VUE_APP_REQUEST_ERROR)
    } else {
      //  处理超时的情况
      let config = error.config
      window.$pop.loading().hide()
      // If config does not exist or the retry option is not set, reject
      // if (!config || !config.retry) return Promise.reject(error)
      if (!config || !config.retry) return Promise.reject(VUE_APP_REQUEST_ERROR)

      // Set the variable for keeping track of the retry count
      config.__retryCount = config.__retryCount || 0

      // Check if we've maxed out the total number of retries
      if (config.__retryCount >= config.retry) {
        // Reject with the error
        // return Promise.reject(error)
        return Promise.reject(VUE_APP_REQUEST_ERROR)
      }

      // Increase the retry count
      config.__retryCount += 1

      // Create new promise to handle exponential backoff
      let backoff = new Promise(function (resolve) {
        setTimeout(function () {
          resolve()
        }, config.retryDelay || 1)
      })

      // Return the promise in which recalls axios to retry the request
      return backoff.then(function () {
        return VUE_APP_REQUEST_ERROR
      })
    }
  }
)

export default {
  post: (url, params, headers) => request({ url: url, method: 'post', data: params, headers }),
  get: (url, params, headers) => request({ url: `${url}?${getUrlParams(params)}`, method: 'get', headers })
}
