/**
 * @desc: 存储游戏大厅的列表数据，用户数据等，展示型数据
 * */
import { make } from 'vuex-pathify'
import { getSecondRechargeExtAct } from '@/api/modules/config'
import { Storage } from '@/utils/helper'
import { getActivityConfig } from '@/api/modules/agent.js'
const state = {
  rechargeUser: {}, // 存放recharge充值时填写的用户信息
  withdrawUser: {}, // 存放withdraw提现时填写的用户信息

  gameCollection: Storage.get('gamelist'), // 游戏数据集合
  tablist: [], // 游戏类型列表

  lobbylist: [], // 大厅入口所有游戏列表数据
  gameInfo: [], // 获取游戏大厅游戏信息
  gameType: [], // 获取游戏大厅游戏类型
  otherFactory: [], // 其他游戏
  gamelist: [],
  languageList: [],
  recent: [],
  games: [],
  collectGames: [],
  phonePlaces: [],
  countryGameLogo: [],
  deskNotices: [],
  gameFactory: {},
  maintains: [], // 维护游戏列表
  pictureUrl: '', //游戏图片或者图标显示域名路径
  /** 传参 */
  experience_list: [], // 试玩游戏的体验权限
  furyInfo: {}, // 页尾说明
  isRotate: false, // 是否刷新金币
  isToGame: false, // 是在游戏中
  pageParameter: {}, // 页面传参
  rechargeInfo: {}, // 获取v3的数据
  withdrawShare: false, // 全民代是否提现成功 -- 用于更新全民代展示信息
  activity: null, //  v2活动数据
  activityInvite: {
    // 邀请活动
  },
  inviteResult: {
    // 邀请活动转盘活动结果
  },
  inviteGiftGold: 0, // 邀请活动赠送金币
  reportLastTime: {}, //  多域名轮询上报功能时间存储
  secondRechargeList: [] // 二充活动是否点开过
}

const getters = {
  //试玩游戏，存在主游戏和子游戏的概念，所以传入的ID在数据遍历时判断
  //return true: 拥有试玩资格
  getExperienceAuth(state) {
    return (gid, gameid) => {
      let l = state.experience_list.filter(v => v.gid == gid)
      if (gameid) l = l.filter(v => v.gameid.includes(gameid))
      return l.length == 0 ? false : true
    }
  }
}

const mutations = make.mutations(state)

const actions = {
  // 邀请活动转盘配置
  getActivityConfigFn: async ({ state, commit }, params) => {
    let res = await getActivityConfig(params)
    if (res.code == 0) {
      commit('SET_ACTIVITY_INVITE', res.data)

      return res
    }
  },
  // 二充追加活动配置接口
  getSecondRechargeExtActFn: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      let res = await getSecondRechargeExtAct(params)
      if (res.code == 0) {
        resolve(res)
      }
    })
  },
  // 清除邀请信息
  clearInviteInfo({ state, commit }) {
    commit('SET_ACTIVITY_INVITE', { ...state.activityInvite, activity_user_task: null })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
