import { EVENT_KEY } from '../eventDefind'

class GoogleAds {
  constructor() {
    this.injectScriptTml = `
        <script async src="https://www.googletagmanager.com/gtag/js?id=#{PIXEL_ID}"></script>
        <script>
          window.dataLayer = window.dataLayer || [];
          function gtag(){dataLayer.push(arguments);}
          gtag('js', new Date());
        
          gtag('config', '#{PIXEL_ID}');
        </script>
        `
    this.eventMap = {
      [EVENT_KEY.registration]: 'CompleteRegistration', // 注册
      [EVENT_KEY.login]: 'Login', // 登录
      [EVENT_KEY.addToCart]: 'AddToCart', // 支付拉起
      [EVENT_KEY.firstPayment]: 'FirstPurchase', // 首次充值
      [EVENT_KEY.completePayment]: 'CompletePayment' // 充值完成
    }

    this.pixelid = null
  }

  init(pixelid) {
    if (!pixelid) return
    this.pixelid = pixelid
    const tagString = this.injectScriptTml.replaceAll('#{PIXEL_ID}', this.pixelid)
    const range = document.createRange()
    const scriptFragment = range.createContextualFragment(tagString)
    document.head.appendChild(scriptFragment)
  }

  logEvent(eventKey, data) {
    if (!window.gtag || !this.pixelid) return
    const eventName = this.eventMap[eventKey]
    console.warn('google ads logEvent', eventName, data || '')
    eventName && gtag('event', eventName, data)
  }
}

export default GoogleAds
