// 邀请有礼活动 -- 转盘 -- 初始化

// activity_info 活动配置
//     status 状态 0:关闭 1:开启
//     start_time 活动开始时间（时间戳）
//     end_time 活动结束时间（时间戳）
//     total_amount 分享奖金数额
//     item_config 奖项配置，json格式，索引数组
//         item_id 奖项id
//         type 奖励类型 0:未中奖 1:直接金币 2:分享金币
//         value 奖励数额
//     draw_num 用户剩余抽奖机会 0:无机会 1:有机会
//     desc 活动描述？
//     version 活动版本
//     share_link 分享url
// activity_user_task 活动用户任务信息（无任务时为null）
//     task_id 任务id
//     total_amount 奖励总金额
//     progress_amount 已完成金额（进度累计余额）
//     task_finish_state 任务完成状态 0:否 1:是
//     task_expire_time 任务过期时间
//     withdraw_state 否已提现 0:否 1:是 2:锁定
//     withdraw_expire_time 提现过期时间
export const getActivityConfig = async params => {
  return await request.post(`/activity.invite_new/config`, params)
}

export const getReceivedRecord = async params => {
  return await request.get(`/activity.invite_new/getReceivedRecord`, params)
}

