// type 奖励类型 0:未中奖 1:直接金币 2:分享金币
// value 奖励数额（type=1为中奖金币，type=2为初始金额）
// total_amount 奖励总金额（仅type=2有效）
export const getActivityLottery = async params => {
  return await request.post(`/activity.invite_new/lottery`, params)
}

