import ImagePreloader from 'image-preloader'
let assets = JSON.parse(process.env.VUE_APP_ASSETS)
const PUBLICPATH = url => {
  if (url.endsWith('.DS_Store')) {
    return null
  }
  return require(`@image/${url}`)
}
let assetCache = []
let blobImg = [
  { key: 'vip_ic_box2', value: null },
  { key: 'vip_ic_box3', value: null },
  { key: 'vip_ic_box4', value: null },
  { key: 'icon_v1', value: null },
  { key: 'icon_v2', value: null },
  { key: 'icon_v3', value: null },
  { key: 'icon_v4', value: null },
  { key: 'relegation_money', value: null },
  { key: 'relegation_banud', value: null },
  { key: 'vip_swiper_bg', value: null },
  { key: 'vip_award_grade', value: null }
]

export const getImageCache = () => {
  try {
    let preloader = new ImagePreloader()
    let files = assets.map(file => ({ name: Object.keys(file)[0], url: PUBLICPATH(`cache/${Object.values(file)}`) }))
    files.forEach(async item => {
      let name = item.name //nameå
      let image = await preloader.preload(item.url)
      assetCache.push({ name, image })
    })
  } catch (error) {
    console.log(error)
  }
}

export const getCacheIcon = key => {
  if (!assetCache.length) return ''

  const imageItem = assetCache.find(item => item.name == key) || {}
  const blobItem = blobImg.find(item => item.key == key) || {}

  if (imageItem && Object.keys(imageItem).length > 0) {
    if (blobItem && Object.keys(blobItem).length > 0) {
      if (blobItem.value) return blobItem.value

      const imgValue = imageItem.image && imageItem.image[0] ? imageItem.image[0].value : ''
      const img = dataURItoBlob(canvas2Img(imgValue))
      // blob对象转换为blob-url
      window.URL = window.URL || window.webkitURL
      const imgUrl = window.URL.createObjectURL(img)
      blobItem.value = imgUrl
      return imgUrl
    } else {
      return imageItem.image && imageItem.image[0] ? imageItem.image[0] : ''
    }
  } else {
    return ''
  }
}

// 根据地址（src）获取图片,生成img对象（标签）
function getImg(src) {
  return new Promise((resolve, reject) => {
    const img = new Image()
    img.src = src
    img.setAttribute('crossOrigin', 'anonymous')
    img.onload = function () {
      resolve(img)
    }
  })
}
// 绘制图片并将canvas转为base64 或blob
export function canvas2Img(img) {
  const canvas = document.createElement('canvas')
  canvas.width = img.width
  canvas.height = img.height

  const ctx = canvas.getContext('2d')
  ctx.drawImage(img, 0, 0) //大小自己限制

  const base64 = canvas.toDataURL('image/png')
  canvas.remove()

  return base64
}

// base64转blob
function dataURItoBlob(base64) {
  let byteString
  const arr = base64.split(',')

  if (arr[0].indexOf('base64') !== -1) {
    // 转回字符串数据
    byteString = atob(arr[1])
  } else {
    // 没有base64标志，使用unescape解码回字符串数据
    byteString = unescape(arr[1])
  }

  // 获取文件格式
  const mimeString = arr[0].split(':')[1].split(';')[0]
  // 创建一个8位无符号整型数组，长度为字符数据长度
  const ia = new Uint8Array(byteString.length)
  for (let i = 0; i < byteString.length; i++) {
    // 存入ascii码
    ia[i] = byteString.charCodeAt(i)
  }
  return new Blob([ia], { type: mimeString })
}

// blob 转 file 一行代码就行了
function blobToFile(blob) {
  return new File([blob], 'file.png', { type: blob.type })
}
// file 转 blob也是一行，毕竟file就是一种特殊的blob
function fileToBlob(file) {
  return new Blob([file], { type: file.type })
}

//全局注册到vue对象中
export default {
  getCacheIcon
}
