import { POP_TIME, MESSAGE_POP_TIME } from '@/api/setting'

export default {
  // 默认打开动画
  dialogFadeIn: {
    animationName: 'dialog-fade-in',
    animationDuration: `${(POP_TIME - 100) / 1000}s`,
    animationTimingFunction: 'linear',
    animationIterationCount: 1
  },
  // 默认关闭动画
  dialogFadeOut: {
    animationName: 'dialog-fade-out',
    animationDuration: `${POP_TIME / 1000}s`,
    animationTimingFunction: 'linear',
    animationIterationCount: 1,
    animationFillMode: 'both'
  },
  // 默认打开动画2
  dialogFadeIn2: {
    animationName: 'dialog-fade-in2',
    animationDuration: `${(POP_TIME - 10) / 1000}s`,
    animationTimingFunction: 'ease-out',
    animationIterationCount: 1
  },
  // 默认关闭动画2
  dialogFadeOut2: {
    animationName: 'dialog-fade-out2',
    animationDuration: `${POP_TIME / 1000}s`,
    animationTimingFunction: 'ease-in',
    animationIterationCount: 1,
    animationFillMode: 'both'
  },
  // 抖动
  dithering: {
    animationName: 'box-trembling',
    animationDuration: `${POP_TIME / 1000}s`,
    animationTimingFunction: 'linear',
    animationIterationCount: 1
  },
  // message 打开动画
  messageFadeIn: {
    animationName: 'message-fade-in',
    animationDuration: `${MESSAGE_POP_TIME / 1000}s`,
    animationTimingFunction: 'linear',
    animationIterationCount: 1
  },
  // message 关闭动画
  messageFadeOut: {
    animationName: 'message-fade-out',
    animationDuration: `${(MESSAGE_POP_TIME - 10) / 1000}s`,
    animationTimingFunction: 'linear',
    animationIterationCount: 1,
    animationFillMode: 'both'
  },
  // 插入默认打开动画
  insert: function (el) {
    if (!el) return
    for (const key in this.dialogFadeIn) el.style[key] = this.dialogFadeIn[key]
  },
  // 关闭默认打开动画
  close: function (el) {
    if (!el) return
    for (const key in this.dialogFadeOut) el.style[key] = this.dialogFadeOut[key]
  },
  // 插入默认打开动画2
  insert2: function (el) {
    if (!el) return
    for (const key in this.dialogFadeIn2) el.style[key] = this.dialogFadeIn2[key]
  },
  // 关闭默认打开动画2
  close2: function (el) {
    if (!el) return
    for (const key in this.dialogFadeOut2) el.style[key] = this.dialogFadeOut2[key]
  },
  // 插入抖动动画
  trembling: function (el) {
    if (!el) return
    for (const key in this.dithering) el.style[key] = this.dithering[key]
  },
  // 插入message打开动画
  insertMes: function (el) {
    if (!el) return
    for (const key in this.messageFadeIn) el.style[key] = this.messageFadeIn[key]
  },
  // 关闭message打开动画
  closeMes: function (el) {
    if (!el) return
    for (const key in this.messageFadeOut) el.style[key] = this.messageFadeOut[key]
  }
}
