import store from '@/store'

/**
 * 防沉迷模块
 * 1. 时间提醒
 * 2. 限制登录
 * 3. 损失限额
 * 4. 每日可玩
 */

// 防沉迷时间提醒
export const respTimeReminder = {
  remindDialogTimeout: null, // 提醒弹窗到达提醒时间 setTimeout
  remindCdTimeout: null, // cd 时间 setTimeout
  remindCdTime: 0, // cd 时间
  // 初始化
  init: function (callback) {
    const { remindItem = {} } = store.state.responsible || {}
    this.remindCdTime = remindItem.edit_cd_time || 0

    if (typeof callback == 'function') callback()
  },
  // 提醒弹窗到达提醒时间
  getReminderTime: function () {
    if (this.remindDialogTimeout) {
      clearTimeout(this.remindDialogTimeout)
      this.remindDialogTimeout = null
    }

    this.remindDialogTimeout = setTimeout(() => {
      this.remindCountDown((data = {}) => {
        // dialogStatus: 1:功能关闭 || 2:弹 || 3:继续执行。 time: 剩余时间
        const { dialogStatus = 1, time = 0 } = data

        // 更新剩余提醒时间
        store.commit('responsible/SET_REMIND_TIME', time)

        // 是否是游客
        const isTourist = store.getters['base/isTourist']
        if (isTourist) {
          clearTimeout(this.remindDialogTimeout)
          this.remindDialogTimeout = null
        } else {
          if (dialogStatus == 1 || dialogStatus == 2) {
            clearTimeout(this.remindDialogTimeout)
            this.remindDialogTimeout = null

            // 更新弹窗状态
            if (dialogStatus == 2) store.commit('responsible/SET_REMIND_DIALOG_SHOW', true)
          }
          if (dialogStatus == 3) this.getReminderTime()
        }
      })
    }, 1 * 1000)
  },
  // 提醒功能cd时间到了
  getReminderEditCd: function () {
    if (this.remindCdTimeout) {
      clearTimeout(this.remindCdTimeout)
      this.remindCdTimeout = null
    }

    this.remindCdTimeout = setTimeout(() => {
      this.remindCdCountDown((num = 0) => {
        // 更新cd时间
        store.commit('responsible/SET_REMIND_CD_TIMER', num)

        // 是否是游客
        const isTourist = store.getters['base/isTourist']
        if (num <= 0 || isTourist) {
          clearTimeout(this.remindCdTimeout)
          this.remindCdTimeout = null
        } else {
          this.getReminderEditCd()
        }
      })
    }, 1 * 1000)
  },
  /**
   * 提醒功能倒计时,判断是否弹窗
   * @param { storage: { lastLogoutTime: 上一次退出时间(秒), currentLoginTime: 当前登录时间(秒), firstDialogTime: 下一次弹窗时间(秒)【 > 0: 还有剩余时间。  = -1: 弹窗未开启或未设置】 } }
   * @returns { dialogStatus: 1:功能关闭 || 2:弹 || 3:继续执行, time: 剩余时间 }
   */
  remindCountDown: function (callback) {
    const { responsible = {}, base = {} } = store.state
    const { responsibleSwitch = 0, remindItem: { status } = {} } = responsible
    const { user: { uid } = {} } = base

    // 是否是游客
    const isTourist = store.getters['base/isTourist']
    // 开关关闭
    if (isTourist || responsibleSwitch != 1 || status != 1) {
      if (typeof callback == 'function') callback({ dialogStatus: 1, time: 0 })
      return
    }

    const storage = window.localStorage.getItem('REMIND_DIALOG_TIME') || '{}'
    let localData = JSON.parse(storage)
    localData = localData[uid] || {}
    const { firstDialogTime = -1 } = localData
    if (firstDialogTime < 0) {
      if (typeof callback == 'function') callback({ dialogStatus: 1, time: 0 })
      return
    }

    // 当前时间
    const currentTime = parseInt(new Date().getTime() / 1000)
    const time = firstDialogTime - currentTime

    if (typeof callback == 'function') callback({ dialogStatus: time <= 0 ? 2 : 3, time: time >= 0 ? time : 0 })
  },
  // cd 时间倒计时
  remindCdCountDown: function (callback) {
    const { responsible = {} } = store.state
    const { responsibleSwitch = 0, remindItem: { status } = {} } = responsible

    // 是否是游客
    const isTourist = store.getters['base/isTourist']
    // 开关关闭
    if (isTourist || responsibleSwitch != 1 || status != 1) {
      if (typeof callback == 'function') callback(0)
      return
    }

    // 剩余时间减少
    if (this.remindCdTime > 0) this.remindCdTime = this.remindCdTime - 1
    if (typeof callback == 'function') callback(this.remindCdTime)
  }
}

// 损失限额
export const respLossLimit = {
  cdTimer: null, // cd 时间 setTimeout
  cdTime: 0, // cd 时间
  // 初始化
  init(callback) {
    const { lossLimitItem = {} } = store.state.responsible || {}
    this.cdTime = lossLimitItem.edit_cd_time || 0
    if (typeof callback == 'function') callback()
  },
  // 失限额数据cd时间到了
  getLossLimitEditCd() {
    if (this.cdTimer) {
      clearTimeout(this.cdTimer)
      this.cdTimer = null
    }
    this.cdTimer = setTimeout(() => {
      this.lossLimitCdCountDown(num => {
        // 更新cd时间
        store.commit('responsible/SET_LOSS_CD_TIMER', num)
        // 是否是游客
        const isTourist = store.getters['base/isTourist']

        if (num <= 0 || isTourist) {
          clearTimeout(this.cdTimer)
          this.cdTimer = null
        } else {
          this.getLossLimitEditCd()
        }
      })
    }, 1 * 1000)
  },
  // cd时间倒计时
  lossLimitCdCountDown(callback) {
    const { responsible = {} } = store.state
    const { responsibleSwitch = 0, lossLimitItem: { status } = {} } = responsible
    // 是否是游客
    const isTourist = store.getters['base/isTourist']
    if (isTourist || responsibleSwitch != 1 || status != 1) {
      if (typeof callback == 'function') callback(0)
      return
    }
    // 剩余时间减少
    if (this.cdTime > 0) this.cdTime = this.cdTime - 1
    if (typeof callback == 'function') callback(this.cdTime)
  }
}

// 每日可玩
export const respDailyPlay = {
  dayPlayTimer: null, // 判断是否在可玩时间内的 setTimeout
  cdTimer: null, // cd 时间 setTimeout
  cdTime: 0, // cd 时间
  // 初始化
  init(callback) {
    const { dayPlayItem = {} } = store.state.responsible || {}
    this.cdTime = dayPlayItem.edit_cd_time || 0
    if (typeof callback == 'function') callback()
  },
  // 可玩时间cd时间倒计时
  getDayPlayEditCd() {
    if (this.cdTimer) {
      clearTimeout(this.cdTimer)
      this.cdTimer = null
    }
    this.cdTimer = setTimeout(() => {
      this.dailyPlayCdCountDown(num => {
        // 更新cd时间
        store.commit('responsible/SET_Day_Play_CD_TIMER', num)
        // 是否是游客
        const isTourist = store.getters['base/isTourist']

        if (num <= 0 || isTourist) {
          clearTimeout(this.cdTimer)
          this.cdTimer = null
        } else {
          this.getDayPlayEditCd()
        }
      })
    }, 1 * 1000)
  },
  // 判断当前时间是否在可玩时间范围内
  hasDayPlayTime() {
    if (this.dayPlayTimer) {
      clearTimeout(this.dayPlayTimer)
      this.dayPlayTimer = null
    }
    this.dayPlayTimer = setTimeout(() => {
      const now = Date.now()
      const { startTime = 0, endTime = 0, day_play_time } = store?.state?.responsible?.dayPlayItem || {}
      if (now > endTime && endTime !== 0) {
        // 当前超出可玩时间，强制退出不进行弹窗提示
        store.dispatch('responsible/disabledPlayLogout', 'logout')
      } else if (day_play_time) {
        this.hasDayPlayTime()
      }
    }, 1 * 1000)
  },
  // cd时间倒计时
  dailyPlayCdCountDown(callback) {
    const { responsible = {} } = store.state
    const { responsibleSwitch = 0, dayPlayItem: { status } = {} } = responsible
    // 是否是游客
    const isTourist = store.getters['base/isTourist']
    // 开关关闭
    if (isTourist || responsibleSwitch != 1 || status != 1) {
      if (typeof callback == 'function') callback(0)
      return
    }
    // 剩余时间减少
    if (this.cdTime > 0) this.cdTime = this.cdTime - 1
    if (typeof callback == 'function') callback(this.cdTime)
  }
}
