import request from '@/api/http.js'

// 配置信息获取
export const getFeatureContent = async params => {
  return await request.post(`/responsible/getFeatureContent`, params)
}

// 保存间隔提醒
export const setReminderTime = async params => {
  return await request.post(`/responsible/setIntervalReminderTime`, params)
}

// 保存配置登录限制
export const setLimitTime = async params => {
  return await request.post(`/responsible/setLoginLimitTime`, params)
}

// 损失限额设置
export const setLossLimit = async params => {
  return await request.post(`/responsible/setLossLimit`, params)
}

// 设置每日可玩时间
export const setDayPlayTime = async params => {
  return await request.post(`/responsible/setDayPlayTime`, params)
}

// 申请禁用
export const applyDisabled = async params => {
  return await request.post(`/responsible/applyDisabled`, params)
}
