import Vue from 'vue'
import _color from '@style/_global/_color.module.scss'

let limit_zindex = Number(_color.currency_wrapper_box_zindex) // 限制页层级
let custom_zindex = Number(_color.custom_dialog_box_zindex) // 自定义弹窗层级
let perform_zindex = Number(_color.custom_game_perform_zindex) // 自定义弹窗层级
let max_zindex = 100010 // 全项目最大层级

/**
 * page: limit:限制页, custom:自定义弹窗, perform:vip升级弹窗, max:开屏时候站点弹窗/登录弹窗
 */
const html = document.querySelectorAll('html')[0]
Vue.directive('zinx', {
  // 指令绑定
  bind(el, { value = {} }) {
    if (!el) return

    const { page = 'custom', isScroll = true } = value
    // 禁止html滚动条
    if (html && isScroll) html.style.overflowY = 'hidden'

    const [mask, ...rest] = el.children || []
    if (mask) {
      switch (page) {
        // 自定义弹窗
        case 'custom':
          custom_zindex++
          mask.style.zIndex = custom_zindex
          break
        // 限制页
        case 'limit':
          limit_zindex++
          mask.style.zIndex = limit_zindex
          break
        // vip升级弹窗
        case 'perform':
          perform_zindex++
          mask.style.zIndex = perform_zindex
          break
        // 全项目最大层级
        case 'max':
          max_zindex++
          mask.style.zIndex = max_zindex
          break

        default:
          mask.style.zIndex = 1500
          break
      }
    }
    if (rest.length > 0) {
      switch (page) {
        // 自定义弹窗
        case 'custom':
          custom_zindex++
          rest.forEach(dom => (dom.style.zIndex = custom_zindex))
          break
        // 限制页
        case 'limit':
          limit_zindex++
          rest.forEach(dom => (dom.style.zIndex = limit_zindex))
          break
        // vip升级弹窗
        case 'perform':
          perform_zindex++
          rest.forEach(dom => (dom.style.zIndex = perform_zindex))
          break
        // 全项目最大层级
        case 'max':
          max_zindex++
          rest.forEach(dom => (dom.style.zIndex = max_zindex))
          break

        default:
          mask.style.zIndex = 1500
          break
      }
    }
  },
  // 指令解除
  unbind(el, { value = {} }) {
    if (!el) return

    const { isScroll = true } = value
    // 解除禁止html滚动条
    if (html && isScroll) html.style.overflowY = 'unset'
  }
})
