const GameList = require('./game')
module.exports = function worker(self) {
  self.addEventListener('message', e => {
    if (e.data.cmd == 'init') {
      g = new GameList(
        e.data.data.gameType,
        e.data.data.logos,
        e.data.data.gameArr,
        e.data.data.pictureUrl,
        e.data.data.languageType
      )
      EVENT_MAP = {
        0: g.getTypes,
        1: g.setFavorate,
        2: g.setRecennt,
        3: g.findMainList,
        4: g.findSubGamelist,
        5: data => g.findSpecialGame(data.id),
        6: g.getGameList,
        7: g.finSubCategory,
        8: g.findGameFactory,
        9: g.favorateUp
      }
      self.postMessage({ cmd: 'init' })
    }
    if (EVENT_MAP[e.data.cmd]) {
      let result = EVENT_MAP[e.data.cmd](e.data.data)
      self.postMessage({ cmd: e.data.cmd, result })
    }
  })
}
