import { make } from 'vuex-pathify'
const modules = require('../../../build/modules.js')

// 组件资源加载器
const state = {
  depositDetailName: '', // 充值组件
  withdrawDetailName: '', // 提现组件
  emailModalName: '', // 移动端站内信组件
  drawerLeftName: '', // 左侧栏组件
  drawerRightName: '', // 右侧栏组件
  mdiCssName: '', // mdi图标
  loginModalName: '', // 登录注册组件
  fontName: '', // 字体
  consoleName: '' // vConsole
}
const getters = {
  // 全局组件配置加载
  globalModules: () => modules?.authorities?.components || []
}
const mutations = make.mutations(state)

const actions = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
