// 特殊加载，使用原生js加载，因为使用addJSLink加载会有问题
const specialLoadServer = ['galacean']

/**
 * 添加/获取加载资源
 * @param {*} name
 * @param {*} urls
 * @param {*} callback
 * @returns
 */
export const getResour = (name = '', urls = [], callback = () => {}) => {
  if (!name || !urls.length) return console.error('Need to pass in urls array')

  if (loadServer[name]) {
    if (loadServer[name].status) {
      callback(true)
    } else {
      loadServer[name].callback = callback
    }
  } else {
    loadServer[name] = {
      status: false,
      callback: callback,
      load: (urlArr = []) => {
        if (specialLoadServer.includes(name)) {
          const loadArr = urlArr.map(src => {
            return new Promise((resolve, reject) => {
              if (src.includes('.js')) {
                const script = document.createElement('script')
                script.src = src
                script.onload = resolve
                script.onerror = reject
                document.body.appendChild(script)
              }
              if (src.includes('.css')) {
                const link = document.createElement('link')
                link.rel = 'stylesheet'
                link.href = src
                link.onload = resolve
                link.onerror = reject
                document.head.appendChild(link)
              }
            })
          })
          Promise.all(loadArr)
            .then(r => {
              loadServer[name].status = true
              loadServer[name].callback(true)
            })
            .catch(e => {
              loadServer[name].status = false
              loadServer[name].callback(false)
            })
        } else {
          addJSLink(urlArr)
            .then(r => {
              loadServer[name].status = true
              loadServer[name].callback(true)
            })
            .catch(e => {
              loadServer[name].status = false
              loadServer[name].callback(false)
            })
        }
      }
    }
    loadServer[name].load(urls)
  }
}
