/**
 * @desc 兼容是否启用worker
 * @method  existWorker         判断当前浏览器是否存在worker对象
 * @method  initGame            初始化游戏数据的API
 * @method  getTabs             获取游戏大厅的游戏类型
 * @method  setFavorates        获取游戏大厅的喜欢/收藏的列表
 * @method  getSubGamelist      获取游戏大厅的对应子游戏的游戏列表
 * @method  getLobbyList        获取游戏大厅首页的所有数据
 * @method  findSpecialGame     获取专题下的游戏数据
 *
 */
import store from '@/store'
import { Storage } from '@/utils/helper'
import work from 'webworkify-webpack'
import GameList from './game'
import { getNewGameList } from '@/api/modules/config'
import { countryGameLogo } from '@/api/modules/game'
import { GAME_TYPE, WHITE_GAME_LIST } from '@/api/setting'

let data_version = ''
export default {
  webworker: {},
  lobbies: [],
  tabs: [],
  isResetGameList: false, // 是否重新调用gamelist数据
  existWorker() {
    let state = false
    if (window.Worker) state = true
    return state
  },
  // 游戏数据
  async getGames() {
    let gameListVersion = localStorage.getItem('gameListVersion')
    let version = store.state.base.dataVersion
    localStorage.setItem('version', version)
    // gameList版本序列号，用来刷新gameList数据 ,目前gameList接口是新的，如果是就的就是要重新调用gamelist
    if (gameListVersion != VUE_APP_VERSION) {
      version = 0
      store.commit('base/SET_DATA_VERSION', 0)
    }
    this.isResetGameList = false
    let result = await getNewGameList({ language: store.state.base.languageType, dataVersion: version }).catch(e => e)
    let logos = await countryGameLogo({ language: store.state.base.languageType }).catch(e => e)
    let res = {}
    if (result.data && result.data.game_type.length > 0) {
      this.isResetGameList = true
      Storage.set('gamelist', result.data)
      // gameList版本序列号，用来刷新gameList数据
      localStorage.setItem('gameListVersion', VUE_APP_VERSION)
      // 判断一下最近游戏列表的游戏是否还在游戏列表中
      const recentList = JSON.parse(JSON.stringify(store.state?.source?.recent || []))
      if (recentList?.length) {
        const gameList = result.data?.game_list || []
        recentList.forEach(item => {
          item.disabled = false
          // 如果厂商不存在禁用
          if (!gameList?.[item.gid]) item.disabled = true
          // 如果该数据是一个子游戏entrance_type==1，并且这个游戏不在列表里
          if (item.entrance_type && !gameList?.[item.gid]?.sub_game_list?.[item.gameid]) item.disabled = true
        })
        store.commit('source/SET_RECENT', recentList)
      }
    }
    if (result.data) {
      data_version = result.data.data_version
      if (version !== result.data.data_version) {
        res = result.data
        this.tabs = this.lobbies = []
      } else {
        res = Storage.get('gamelist')
      }
    } else {
      this.tabs = this.lobbies = []
    }

    return {
      list: res,
      logos: logos.data
    }
  },
  saveStore(params, data, callback) {
    this.subscribe(params)
      .then(_ => this.middleTask('init'))
      .then(() => {
        // store.commit("source/SET_GAME_FACTORY", data.game_factory);  //获取游戏产商信息
        // store.commit("source/SET_EXPERIENCE_LIST", data.experience_list);//获取试玩体验权限
        store.commit('source/SET_GAME_INFO', data.game_list) // 获取游戏大厅游戏信息
        store.commit('source/SET_GAME_TYPE', data.game_type) // 获取游戏大厅游戏类型
        store.commit('source/SET_TABLIST', data.game_lobby_type || []) // 获取游戏大厅tab游戏类型
        store.commit('source/SET_PICTURE_URL', data.picture_url) // 获取游戏大厅游戏图片或者图标显示域名路径
        store.commit('source/SET_OTHER_FACTORY', this.gameFactory(data.other_game_factory, data.picture_url)) // 游戏地下产商信息
      })
      .then(_ => callback(this.isResetGameList))
  },
  async initGame(callback, useWorkerState) {
    let { list, logos } = await this.getGames()
    let data = list
    if (this.existWorker()) {
      store.commit('base/SET_DATA_VERSION', data.data_version || 0) //设置游戏列表时间版本
      store.commit('source/SET_COUNTRY_GAME_LOGO', logos) // 获取游戏大厅涉及到的图标资源
      let params = {
        cmd: 'init',
        data: {
          gameType: data,
          logos: logos,
          gameArr: store.state.base.user?.user_config?.game_arr,
          pictureUrl: data.picture_url,
          languageType: store.state.base.languageType
        }
      }

      if (this.webworker && this.webworker.terminate) this.webworker.terminate()
      this.webworker = work(require.resolve('./gameWorker.js'))
      this.saveStore(params, data, callback)
    } else {
      //走API
      this.webworker = this.wk(
        list,
        logos,
        store.state.base.user.user_config.game_arr,
        data.picture_url,
        store.state.base.languageType
      )
      // store.commit("source/SET_GAME_FACTORY", data.game_factory);  //获取游戏产商信息
      // store.commit("source/SET_EXPERIENCE_LIST", data.experience_list);//获取试玩体验权限
      store.commit('source/SET_GAME_INFO', data.game_list) // 获取游戏大厅游戏信息
      // store.commit("source/SET_NEW_GAME_INFO", data.game_list);    // 新的获取游戏大厅游戏信息
      store.commit('source/SET_GAME_TYPE', data.game_type) // 获取游戏大厅游戏类型
      store.commit('source/SET_TABLIST', data.game_lobby_type || []) // 获取游戏大厅tab游戏类型
      store.commit('source/SET_PICTURE_URL', data.picture_url) // 获取游戏大厅游戏图片或者图标显示域名路径
      store.commit('source/SET_OTHER_FACTORY', this.gameFactory(data.other_game_factory, data.picture_url)) // 游戏地下产商信息
      callback(this.isResetGameList)
    }
  },
  middleTask(cmd) {
    return new Promise(resolve => {
      this.webworker.addEventListener('message', e => {
        let res = e.data
        resolve(res.result)
      })
    })
  },
  //订阅事件
  async subscribe({ cmd, data }) {
    this.webworker.postMessage({ cmd, data })
  },
  // 获取大厅tab数据
  getTabs() {
    const { tablist = [] } = store.state.source
    if (tablist.length > 0) {
      let pictureUrl = store?.state?.source?.pictureUrl
      let newTablist = tablist.map(item => {
        return {
          ...item,
          icon: pictureUrl + item.icon,
          icon_on: pictureUrl + item.icon_on
        }
      })
      return Promise.resolve(newTablist)
    }
    return []
  },
  // 收藏
  setFavorates() {
    return this.subscribe({ cmd: 1 })
      .then(_ => this.middleTask(1))
      .then(list => {
        store.commit('source/SET_COLLECT_GAMES', list)
      })
  },
  //更新收藏信息
  favorateUp() {
    this.subscribe({ cmd: 9, data: { list: store.state.source.collectGames } })
      .then(_ => this.middleTask(9))
      .then(list => {
        store.commit('source/SET_COLLECT_GAMES', list)
      })
  },
  // 最近记录
  getRecentList() {
    return this.subscribe({ cmd: 2 })
      .then(_ => this.middleTask(2))
      .then(list => {
        store.commit('source/SET_RECENT', list)
      })
  },
  // 全部游戏
  getLobbyList() {
    if (store.state.source.lobbylist.length > 0 && localStorage.getItem('version') == data_version) {
      store.commit('source/SET_LOBBYLIST', store.state.source.lobbylist)
      // store.commit('source/SET_LOBBYLIST', store.state.source.lobbylist);
      return Promise.resolve(store.state.source.lobbylist)
    }
    return this.subscribe({ cmd: 3 })
      .then(_ => this.middleTask(3))
      .then(res => {
        const list = res ? res.filter(v => !WHITE_GAME_LIST.includes(v.id)) : []
        store.commit('source/SET_LOBBYLIST', list)
        this.lobbies = list
      })
  },
  // tab切换，搜索专项游戏
  findSpecialGame(id, categoryType) {
    if (GAME_TYPE.LOBBY == categoryType && this.lobbies.length > 0) {
      store.commit('source/SET_GAMELIST', this.lobbies)
      return Promise.resolve(this.lobbies)
    }
    return this.subscribe({ cmd: 5, data: { id } })
      .then(_ => this.middleTask(5))
      .then(list => {
        store.commit('source/SET_GAMELIST', list)
      })
  },
  getSubGamelist(id) {
    return this.subscribe({ cmd: 4, data: { id } })
      .then(_ => this.middleTask(4))
      .then(list => {
        store.commit('source/SET_GAMELIST', list)
      })
  },
  finSubCategory(id, sub_id) {
    return new Promise((resolve, _reject) => {
      this.subscribe({ cmd: 7, data: { id, sub_id } })
        .then(_ => this.middleTask(7))
        .then(data => {
          store.commit('source/SET_PAGE_PARAMETER', data)
          resolve(data)
        })
    })
  },
  findGameFactory(id) {
    return new Promise((resolve, _reject) => {
      this.subscribe({ cmd: 8, data: { id } })
        .then(_ => this.middleTask(7))
        .then(data => {
          store.commit('source/SET_GAMELIST', data)
          resolve(data)
        })
    })
  },
  destroyed() {
    setTimeout(() => {
      if (this.webworker) this.webworker.terminate()
    }, 300)
  },
  //  游戏地下产商信息图片加域名
  gameFactory(list, host) {
    if (list && list.length > 0) {
      return list.map(item => {
        return {
          ...item,
          img_url: host + item.img_url
        }
      })
    }
    return []
  },
  //兼容web worker的API
  wk(list, logos, gameArr, pictureUrl, languageType) {
    let g = new GameList(list, logos, gameArr, pictureUrl, languageType)
    let EVENT_MAP = {
      0: g.getTypes,
      1: g.setFavorate,
      2: g.setRecennt,
      3: g.findMainList,
      4: g.findSubGamelist,
      5: data => g.findSpecialGame(data.id),
      6: g.getGameList,
      8: g.findGameFactory,
      9: g.favorateUp
    }
    return {
      data: {},
      postMessage({ cmd, data }) {
        this.data = EVENT_MAP[cmd](data)
        return Promise.resolve({
          cmd: cmd,
          result: EVENT_MAP[cmd](data)
        })
      },
      addEventListener(event, callback) {
        return callback({
          data: { result: this.data }
        })
      }
    }
  }
}
