import request from '@/api/http.js'

// 获取回归任务列表
export const getRegressionList = async params => {
  return await request.post(`/task.back_task/getList`, params)
}

// 领取奖励
export const getRegressionReceiveGold = async params => {
  return await request.post(`/task.back_task/receiveGold`, params)
}

// 获取回归任务弹窗
export const getRegressionDialog = async params => {
  return await request.post(`/task.back_task/getwindow`, params)
}

// 获取回归任务-七日签到列表
export const getRegressionSingleList = async params => {
  return await request.post(`/task.back_task/getSingleAction`, params)
}

// 获取回归任务-七日签到领取奖励
export const getRegressionSingleReceiveGold = async params => {
  return await request.post(`/task.back_task/singleReceiveGold`, params)
}
