import store from '@/store'
import VipType from '@/events/vip.js'
import kefuType from '@/events/kefu.js'
import menuType from '@/events/menu.js'
import PublishSubscribe from 'publish-subscribe-js'
import { $t } from '@/utils/i18n.js'
import { AI_MESSAGE_EVENT } from '@/events/notice.js'
import { AdjustlogEvent } from '@/utils/helper.js'
import { luckyType, regressionType } from '@/events/game.js'

export default {
  $app: {},
  $time: null,
  fn: {
    $app: {},
    $redDotStr: {
      clean_bet: 'clean_bet',
      free_lose: 'free_lose',
      mail: 'mail',
      notice: 'notice',
      recharge_send_activity: 'recharge_send_activity',
      sign_reward: 'sign_reward',
      spread_way: 'spread_way',
      unclaimed_task: 'unclaimed_task',
      user_spread_way: 'user_spread_way',
      vip_cash_gift: 'vip_cash_gift',
      week_month_card: 'week_month_card',
      task_red: 'task_red',
      kf_reply_user_question: 'kf_reply_user_question',
      ai_promote: 'ai_promote'
    },
    $throttle: {
      mailTimer: null // 站内信节流倒计时
    },
    // 刷新 vip 数据
    refreshVip() {
      // 如果在vip页面，重新请求接口刷新数据
      // if (this.$app.$route.name == 'recompensas' || this.$app.$route.name == 'menu') {
      // 发送
      PublishSubscribe.publish(VipType.RENEW_LIST)
      // };
    },
    // 更新用户金币
    updateGold(gold) {
      if (!gold) return
      store.commit('base/SET_USER', { ...store.state.base.user, gold })
    },

    // 刷新用户信息
    async refreshGold(app, callback) {
      await store.dispatch('base/refreshAccount')
      if (typeof callback == 'function') callback()
    },
    // 充值成功弹窗提示
    withdrawGoldTips(data) {
      const { send_gold, gold } = data
      const goldData = send_gold > 0 ? gold + '  +  ' + send_gold : gold
      // 插入弹窗序列
      PublishSubscribe.publish(store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: $t('UI_Code_Bonus'),
          value: goldData
        }
      })

      this.refreshVip()
      // 这个时候重新更新一下用户信息【因为充值成功之后 ws 回调是先回调刷新用户信息，然后才回调充值成功，如果正式环境正常，再屏蔽这边】
      this.refreshGold('', () => {
        // 序列中插入生涯弹窗
        PublishSubscribe.publish(store.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'career', seat: 'first' })
      })
      // 通知幸运转盘更新
      PublishSubscribe.publish(luckyType.RENEW_CONFIG)
      // 通知回归活动更新
      PublishSubscribe.publish(regressionType.REGRESSION_CONFIG)
    },
    /**
     * VIP升级弹窗表现
     * level: vip 等级
     */
    updateVIPLevel(level = 1) {
      // 更新红点信息
      store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'clean_bet',
        language: store.state.base.languageType,
        token: store.state.base.user.token
      })
      store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'vip_cash_gift',
        language: store.state.base.languageType,
        token: store.state.base.user.token
      })
      this.refreshGold()
      // 储存vip升级弹窗,插入到最前
      PublishSubscribe.publish(store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'perform',
        seat: 'first',
        params: {
          level: store.getters['base/getVipCurrentLevel'](level)
        }
      })

      this.refreshVip()
      // 通知幸运转盘更新
      PublishSubscribe.publish(luckyType.RENEW_CONFIG)
    },
    // 游戏回到主页
    bg_close() {
      // this.$app.$router.push(ROUTES[0].redirect);
    },
    // updateWithdrawType 提现功能回调刷新
    updateWithdraw() {
      const { updateWithdrawType } = store.state.modal
      setTimeout(() => {
        store.commit('modal/SET_UPDATE_WITHDRAW_TYPE', !updateWithdrawType)
      }, 1000)
    },
    getRechargeGold(type) {
      store
        .dispatch('base/getRechargeGoldFun', {
          language: store.state.base.languageType,
          token: store.state.base.user.token
        })
        .then(res => {
          const { data } = res
          if (data.length > 0) {
            const { amount = 0 } = data[0]
            AdjustlogEvent(store.state['base']?.systemConfig, type, amount)
          }
        })
    },
    // 客服消息回调方法
    userQuestion(data = {}) {
      const { messageData = {} } = data
      store.commit('base/SET_KF_NOTICE_DATA', messageData)
      // 发布订阅, 更新工单红点
      PublishSubscribe.publish(kefuType.KEFU_WORK_ORDER_RED, messageData)
    },
    // 新版智能推广（AI）消息回调
    handleAiMessage(content) {
      // 更新红点信息
      store.dispatch('base/updateLocalDotValue', { key: 'ai_promote', is_show: 1 })
    },
    // 体育赛事消息回调方法
    betbySportMoney(data = {}) {
      const { end_gold = 0 } = data
      store.commit('sport/SET_BETBY_MONEY', end_gold)
    },
    // 更新红点【目前了解到的只适用于站内信，具体服务端为啥这样做，咱也不敢说，咱也不敢问】
    renewRedDot(data = {}) {
      const { show_type = 0, event = '' } = data
      if (show_type == 0) {
        // show_type  本地更新
        const { showRedDot = {} } = store.state.base
        // mail 更新事件
        if (event == this.$redDotStr.mail) {
          let { value = 0 } = showRedDot[event] || {}
          store.dispatch('base/updateLocalDotValue', { key: event, is_show: 1, value: value++ })
        } else {
          store.dispatch('base/updateLocalDotValue', { key: event, is_show: 1 })
        }
      } else if (show_type == 2) {
        const { uid = 0 } = store.state.base.user
        let delay = ((uid % 10) + 1) * 5 + (Math.random() * 10 - 5)
        setTimeout(() => {
          store.dispatch('base/showRedDotReceivedFun', {
            language: store.state.base.languageType,
            token: store.state.base.user.token,
            version: 2,
            event
          })
        }, delay * 1000)
      } else {
        if (event == this.$redDotStr.notice || event == this.$redDotStr.kf_reply_user_question) {
          // 判断如果是上面两个本地更新，其他走接口更新
          store.dispatch('base/updateLocalDotValue', { key: event, is_show: 1 })
        } else {
          const { user = {}, languageType = '' } = store.state.base
          const { token = '' } = user

          // 站内信做节流
          if (event == this.$redDotStr.mail) {
            if (this.$throttle.mailTimer) return

            this.$throttle.mailTimer = setTimeout(() => {
              store.dispatch('base/showRedDotReceivedFun', { language: languageType, token, version: 2, event })
              clearTimeout(this.$throttle.mailTimer)
              this.$throttle.mailTimer = null
            }, 30 * 1000)
          } else {
            store.dispatch('base/showRedDotReceivedFun', { language: languageType, token, version: 2, event })
          }
        }
      }
    },
    // 广播接收
    sendBroadcast(data = {}) {
      // 发送广播
      PublishSubscribe.publish(menuType.MENU_BROADCAST, data.text || '')
    },
    // 防沉迷
    getResponsible(data = {}) {
      // 因为服务端缓存问题，所以得缓存
      setTimeout(() => {
        store.dispatch('responsible/getWsFeatureInfo', data.data)
      }, 3 * 1000)
    },
    // 邀请活动转盘配置
    getActivityConfig(data = {}) {
      store.dispatch('source/getActivityConfigFn', {
        language: store.state.base.languageType,
        token: store.state.base.user.token
      })
    },
    // 外部活动列表
    getEXtGameActivityList(data = {}) {
      if (data.key) {
        const oldArr = store.state.base.gameActivityList.slice()
        const index = oldArr.findIndex(e => e.key === data.key)
        index === -1 ? oldArr.push(data) : oldArr.splice(index, 1)
        store.commit('base/SET_GAME_ACTIVITY_LIST', oldArr)
      }
    },
    // 损失限额
    onResponsibleChange(data) {
      const status = data.is_limit || 0
      store.commit('responsible/SET_LOSS_LIMIT_STATUS', status)
      const isToGame = store.state.source.isToGame
      if (status == 1) {
        // 达到每日限额
        this.$app.$pop.message().show({ content: $t('UI.Resp_Loss_Limit_Tips_3') })
      } else if (status == 2) {
        // 达到每周限额
        this.$app.$pop.message().show({ content: $t('UI.Resp_Loss_Limit_Tips_4') })
      } else if (status == 3) {
        // 达到每月限额
        this.$app.$pop.message().show({ content: $t('UI.Resp_Loss_Limit_Tips_5') })
      }
      if (status != 0 && isToGame) {
        // 达到限额且在游戏中，则关闭游戏
        this.$app.$pop.game().closeMiddle()
      }
    },
    // 人脸识别开关
    getFaceSwitch(data = {}) {
      const { systemConfig = {} } = store.state.base || {}
      store.commit('base/SET_SYSTEM_CONFIG', { ...systemConfig, rekognition_switch: data.data || 0 })
    },
    // 人脸识别解绑通知
    getFaceStatus(data = {}) {
      const { user = {} } = store.state.base || {}
      store.commit('base/SET_USER', { ...user, rekognition_status: data.data || 0 })
    },
    // 洗码开关
    getBetStatus(data = {}) {
      const { systemConfig = {} } = store.state.base || {}
      const { status = 0 } = data

      store.commit('base/SET_SYSTEM_CONFIG', { ...systemConfig, vip_clean_bet_switch: status })
      // 当前在洗码页，返回首页
      if (this.$app.$route.name == 'washCode' && status != 1) this.$app.$router.push({ name: 'menu' })
    }
  },
  // 监听被踢下线
  EVENT_LOGOUT(data) {
    if (!this.$app) return
    if (!store.state.source.isToGame) {
      // 回到首页
      this.$app.$router.push({ name: 'menu' })

      // 唤起提示弹窗
      store.dispatch('base/logout').then(() => {
        // 关闭客服弹窗
        this.$app.$pop.kefu().hide()

        this.$app.$pop
          .kick({
            title: $t('General.General_9'),
            content: $t('UI.System_43'),
            submitText: $t('General.General_1'),
            success: () => {
              // 唤起登录弹窗
              store.dispatch('modal/setLoginRegistShow')
            }
          })
          .show()
      })
    } else {
      store.commit('base/SET_IS_FORCED', true)
    }
  },
  // 监听登录后的回调
  EVENT_LOGIN() {
    if (!this.$app) return
    // 如果在vip页面被踢下线，然后重新登录，会在当前页面，也要刷新数据
    this.fn.refreshVip(this.$app)
  },
  // 服务端踢出
  EVENT_FORCED_KICK(content) {
    // 回到首页
    this.$app.$router.push({ name: 'menu' })

    // 唤起提示弹窗
    store.dispatch('base/logout').then(() => {
      // 关闭客服弹窗
      this.$app.$pop.kefu().hide()

      this.$app.$pop
        .kick({
          title: '',
          content: content ? content : $t('UI_LOGINOUT_INFO'),
          submitText: $t('General.General_1'),
          success: () => {
            // 唤起登录弹窗
            store.dispatch('modal/setLoginRegistShow')
          }
        })
        .show()
    })
  },
  // 弹窗回调
  EVENT_NOTIFY(content) {
    if (!this.$app) return
    // const content = JSON.parse(data.content)
    // 刷新用户信息
    if (['updateUserInfo'].includes(content.type)) this.fn.refreshGold(this.$app)
    // if (['updateUserInfo'].includes(content.type)){
    //     const { UpdateUserinfo = {} } = content.body;
    //     const { GoldCoin =''} =UpdateUserinfo
    //     store.commit("base/SET_USER", {...store.state.base.user, gold: GoldCoin});
    // }
    // 充值成功弹窗提示（因为回调是先弹窗然后才刷新用户信息，刷新vip信息需要新的用户 token， 延迟一下请求）
    // 检查是否需要打点
    if (['common_check'].includes(content.type)) {
      // 如果延迟5秒内不然触发打点
      if (this.$time) return
      this.$time = setTimeout(() => {
        store.dispatch('base/getAjNoticeFun').then(_res => {
          this.$time = null
        })
      }, 5000)
    }
    //  自定义打点
    if (['common_suc'].includes(content.type)) {
      if (content.data.length > 0) {
        try {
          content.data.forEach(element => {
            AdjustlogEvent(store.state['base']?.systemConfig, element.key, element.value)
          })
        } catch (error) {}
      }
    }
    ///  充值调用adjust 打点
    // if (['gold_collection_tips'].includes(content.type) || ['online_payment', 'back_stage_recharge', 'manual_payment', 'company_payment'].includes(content.source)) {
    //     this.fn.getRechargeGold('recharge');
    // }
    //  充值回调弹窗刷新
    if (['gold_collection_tips'].includes(content.type)) {
      const { updateDepositType } = store.state.modal
      setTimeout(() => {
        store.commit('modal/SET_UPDATE_DEPOSIT_TYPE', !updateDepositType)
      }, 1000)
      setTimeout(() => {
        this.fn.withdrawGoldTips(content)
      }, 1 * 1000)
    }
    // apk 活动金币领取通知
    if (['apk_recharge_act_status'].includes(content.type)) {
      const { apkActivityGold = {} } = store.state.base
      apkActivityGold.apk_show_switch = content.status // 关闭下载金币提示
      store.commit('base/SET_APK_ACTIVITY_GOLD', apkActivityGold)
    }

    // 首充七日签到
    if (['first_recharge_sign'].includes(content.type)) {
      const { user = {} } = store.state.base
      const newUser = { ...user, first_recharge_sign_switch: content.status || 0 }
      store.commit('base/SET_USER', newUser)

      store.dispatch('base/showRedDotReceivedFun', {
        language: store.state.base.languageType,
        token: store.state.base.user.token,
        version: 2,
        event: 'first_recharge_sign_dot'
      })
    }

    // 判断红点更新【目前了解到的只适用于站内信】
    if (['red_dot_event_notice'].includes(content.type)) this.fn.renewRedDot(content)
    // 修改密码后退出登录
    if (['user_outline'].includes(content.type)) this.EVENT_ACCOUNT_SEAL({}, $t('UI.Change_Pwd_Text'))
    // 提现功能回调刷新
    // if (['withdraw_suc'].includes(content.type)) {
    //     ///  首次充值调用adjust 打点  key common2
    //     store.dispatch("base/getWithdrawGoldFun", { language: store.state.base.languageType, token: store.state.base.user.token }).then((res) => {
    //         const { data } = res;
    //         if (data.length > 0) {
    //             const { amount = 0 } = data[0];
    //             AdjustlogEvent(store.state["base"]?.systemConfig, 'withdraw', amount);
    //         }
    //     })
    //     // refreshGold();
    //     this.fn.updateWithdraw();
    // }
    // 游戏维护通知
    if (['game_maintain_notice'].includes(content.type)) {
      let games = JSON.parse(data.content).data
      let tp = []
      let gamelist = store.state.source.maintains

      if (gamelist.length == 0) {
        gamelist = gamelist.concat(games)
      } else {
        gamelist = gamelist.map(gm => {
          let tt = games.find(t => t.gid == gm.gid)
          if (tt) return tt
          if (!tt) return gm
        })
        tp = games.filter(ga => {
          return gamelist.findIndex(m => m.gid == ga.gid) == -1
        })
        gamelist = gamelist.concat(tp)
        // if(gamelist.findIndex(v => v.gid == )){}
      }
      gamelist = gamelist.map(item => ({ ...item, mtc_msg: item.msg_en }))
      store.commit('source/SET_MAINTAINS', gamelist)
    }
    // VIP升级弹窗表现
    if (content.type == 'vip_lv') this.fn.updateVIPLevel(content.vip_id)
    // 游戏回到主页
    if (content.type == 'bg_close') this.fn.bg_close()
    // if() refreshGold(this.$app);
    // 保级解锁通知
    if (content.type == 'unlock_user_level') this.fn.refreshGold(this.$app)
    // 客服消息回调
    if (content.type == 'kf_reply_user_question') this.fn.userQuestion(content)
    // 新版智能客服（AI）通知
    if (content.type == AI_MESSAGE_EVENT) this.fn.handleAiMessage(content)
    // 体育赛事金币回调
    if (content.type == 'betby_sport') this.fn.betbySportMoney(content)
    // 广播
    if (content.type == 'game_broadcast' || content.type == 'gold_withdrawal') this.fn.sendBroadcast(content)
    // 防沉迷
    if (content.type == 'responsible_change') this.fn.getResponsible(content)
    // 邀请活动转盘实时状态回调
    if (content.type == 'refresh_invate_num') this.fn.getActivityConfig(content)
    // 邀请活动转盘没有任务回首页
    if (content.type == 'invite_task_end' || content.type == 'invite_task_refuse_end') {
      this.$app.$router.push({ name: 'menu' })
      this.fn.getActivityConfig(content)
    }
    // 外部活动
    if (content.type == 'ext_game_activity') this.fn.getEXtGameActivityList(content.data || {})
    // 达到损失限额
    if (content.type == 'user_status') this.fn.onResponsibleChange(content.data || {})
    // 人脸识别开关
    if (content.type == 'rekognition_switch') this.fn.getFaceSwitch(content)
    // 人脸识别解绑通知
    if (content.type == 'rekognition_status') this.fn.getFaceStatus(content)
    // 洗码开关
    if (content.type == 'trigger_vip_clean_bet_switch') this.fn.getBetStatus(content)
  },
  EVENT_OUT_CONNECTED(data) {
    // throttle(() => {
    //     if (!store.state.source.isToGame) {
    //         loadWebsocket(this.$app);
    //     }
    // }, 5000, false);
  },
  EVENT_UPDATE_GOLD(data) {
    console.log('EVENT_UPDATE_GOLD', data)
    this.fn.updateGold(data)
  }
}
