import { EVENT_KEY, ADS_TYPE } from './eventDefind'
import FirebaseAds from './modules/firebase'
import TiktokAds from './modules/tiktok'
import FacebookAds from './modules/facebook'
import KwaiAds from './modules/kwai'
import GoogleAds from './modules/google'
import { FIREBASE_APPID } from '@/api/setting'

const pixelAdsConfig = [
  {
    key: 'tiktok_id',
    type: ADS_TYPE.tiktok,
    ads: TiktokAds
  },
  {
    key: 'kwai_id',
    type: ADS_TYPE.kwai,
    ads: KwaiAds
  },
  {
    key: 'google_ads_id',
    type: ADS_TYPE.google,
    ads: GoogleAds
  },
  {
    key: 'facebook_pixelid',
    type: ADS_TYPE.facebook,
    ads: FacebookAds
  }
]
let adsCaches = {}
export function createPixelAds(id, type, AdsServer) {
  if (!id) return
  const adsServer = new AdsServer()
  adsServer.init(id)
  adsCaches[type] = adsServer
}

export function initAds() {
  pixelAdsConfig.forEach(item => {
    const pixelid = localStorage.getItem(item.key)
    createPixelAds(pixelid, item.type, item.ads)
  })
  // firebase
  if (FIREBASE_APPID) {
    const firebaseAds = new FirebaseAds()
    firebaseAds.init()
    adsCaches[ADS_TYPE.firebase] = firebaseAds
  }
}

// 打点事件
/**
 *
 * @param {String} eventKey
 * @param {*} data
 */
export function logEvent(eventKey, options = {}) {
  const { type, params } = options
  if (!eventKey) return
  let adsTypes = []
  if (typeof type === 'string') {
    adsTypes.push(type)
  } else if (Array.isArray(type)) {
    adsTypes = type
  } else {
    adsTypes = Object.keys(adsCaches)
  }

  adsTypes.forEach(t => {
    const ads = adsCaches[t]
    if (ads) {
      const eventMap = ads.eventMap
      eventMap[eventKey] && ads.logEvent(eventKey, params)
    }
  })
}

// 动态初始化ads服务
export function dynamicInitAds(type, id) {
  // 已存在时无需处理
  if (type && adsCaches[type]) return
  const config = pixelAdsConfig[type]
  if (!config) return
  localStorage.setItem(config.key, id)
  createPixelAds(id, type, config.ads)
}

// 获取facebook pixelid
export function getFacebookPixelId() {
  const facebookAds = adsCaches[ADS_TYPE.facebook]
  return facebookAds?.pixelid
}

export { EVENT_KEY, ADS_TYPE }
