import { MsgEventDefine } from './enums/MsgEventDefine'
import PushServerType from './enums/PushServerType'

function notDisconnectState(key, data) {
  return key != MsgEventDefine.EVENT_DISCONNECT && typeof data == 'string'
}

const GATE_AUTH_ACTION = 'gate.user.auth' // 登录鉴权
const HANDSHAKE_ACK = 1 // 发送心跳包内容
const HANDSHAKE_RESP = 'pong' // 心跳包回复
const DEFAULT_MAX_RECONNECT_COUNT = 5 // 最大重连次数
const RECONNECT_DELAY = 2000 // 重连延迟时间（毫秒）

class SocketServer {
  constructor(url, token) {
    this.url = url
    this.token = token
    this.socket = null
    this.eventList = {}
    this.isInit = false
    this.reconnect_count = 1 // 重连次数
    this.reconnectTimer = null // 重连定时器
    this.needReconnect = true // 是否需要重连
    this.pingInterval = 50 * 1000 // 心跳间隔
    this.pingTimeout = 10000 // 心跳超时时间
    this.pingTimeoutId = null // 心跳定时器
    this.pongTimeoutId = null // 心跳超时定时器
  }

  /**
   * 添加事件监听
   * @param {*} cmd
   * @param {*} handler
   */
  addHandler(cmd, handler) {
    if (this.eventList[cmd]) {
      console.warn('event:' + cmd + "' handler has been registered.")
      return
    }
    const handlerFn = data => {
      if (notDisconnectState(cmd, data)) data = JSON.parse(data)
      handler(data)
    }

    this.eventList[cmd] = handlerFn
  }

  getHandler(cmd) {
    const fn = this.eventList[cmd]
    if (fn) return fn
    return () => {
      console.warn('unregistered event:' + cmd)
    }
  }

  connect() {
    this.needReconnect = true
    this.socket = new WebSocket(this.url)
    // 监听socket连接状态
    this.socket.onopen = () => {
      this.isInit = true
      this.needReconnect = true
      this.reconnect_count = 1
      this.gateAuth()
      this.heartbeatDetection()
    }
    this.socket.addEventListener('message', event => {
      this.handleMessage(event)
      this.heartbeatDetection()
    })

    this.socket.onerror = error => {
      console.error('socket error', error)
    }

    this.socket.onclose = e => {
      console.error('socket closed', e)
      this.socket = null
      this.needReconnect && this.reconnet()
    }
  }

  close() {
    this.socket && this.socket.close()
    this.isInit = false
    this.needReconnect = false
    this.eventList = {}
    this.socket = null
    clearTimeout(this.reconnectTimer)
    clearTimeout(this.pingTimeoutId)
    clearTimeout(this.pongTimeoutId)
  }
  send(message) {
    if (this.isInit) {
      this.socket && this.socket.send(typeof message === 'object' ? JSON.stringify(message) : message)
    }
  }

  handleMessage(event) {
    // 心跳检测
    if (event.data === HANDSHAKE_RESP) {
      return
    }
    let message = {}
    try {
      message = JSON.parse(event.data)
    } catch (e) {
      console.error('parse recv data error')
      return
    }

    const { type, code, data } = message
    if (code !== 0) return
    switch (type) {
      // 登录鉴权成功
      case PushServerType.UserAuth:
        this.getHandler(MsgEventDefine.EVENT_LOGIN)()
        break
      case PushServerType.UserNotify:
        this.getHandler(MsgEventDefine.EVENT_NOTIFY)(data)
        break
      case PushServerType.UpdateGold:
        this.getHandler(MsgEventDefine.EVENT_UPDATE_GOLD)(data)
        break
      case PushServerType.RepeatLogin:
        this.getHandler(MsgEventDefine.EVENT_LOGOUT)()
        this.close()
        break
      // todo
      // case PushServerType.ForcedKick:
      //   this.getHandler(MsgEventDefine.EVENT_LOGOUT)()
      //   break
      default:
        break
    }
  }

  // 发送心跳包
  heartbeatDetection() {
    if (this.socket && this.socket.readyState === this.socket.CLOSED) return
    clearTimeout(this.pingTimeoutId)
    clearTimeout(this.pongTimeoutId)
    this.pingTimeoutId = setTimeout(() => {
      this.sendPing()
      this.checkPingTimeout()
    }, this.pingInterval)
  }

  sendPing() {
    this.send(HANDSHAKE_ACK)
  }

  checkPingTimeout() {
    this.pongTimeoutId = setTimeout(() => {
      this.socket && this.socket.close()
    }, this.pingTimeout)
  }

  // 重新连接socket
  reconnet() {
    this.reconnect_count++
    // 重连最大次数
    if (this.reconnect_count > DEFAULT_MAX_RECONNECT_COUNT) {
      console.error(`WebSocket reconnect failed after ${this.reconnect_count} attempts`)
      return
    }
    console.log(`Attempting to reconnect WebSocket, attempt ${this.reconnect_count}`)
    setTimeout(() => {
      this.connect()
    }, RECONNECT_DELAY)
  }

  // 登录
  gateAuth() {
    this.send({
      route: GATE_AUTH_ACTION,
      data: { token: this.token }
    })
  }
}
export default SocketServer
