import Vue from 'vue'
import Router from 'vue-router'
import routes from './routes'
import init from './init.js'
import PublishSubscribe from 'publish-subscribe-js'

Vue.use(Router)

// cv以下代码解决路由地址重复的报错问题(一劳永逸)
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const router = new Router({ routes })

/**
 * 判断是否弹出登录弹窗
 * 游客 && 非特定页面
 */
const notPage = [
  'menu',
  'recompensas',
  'washCode',
  'sportView',
  'activity',
  'featured',
  'gameChild',
  'pesquisa',
  'stopNotice',
  'limit',
  'collapse',
  'login',
  'activityDetail',
  'helpInfo'
]
const validateLoginState = (store, to, from) => {
  if (store.getters['base/isTourist'] && !notPage.includes(to.name)) {
    store.dispatch('modal/setLoginRegistShow')
    return false
  }
  return true
}

// 路由拦截
let routeState = false
const createRouter = store => {
  router.beforeEach((to, from, next) => {
    // 判断当前是否是初始化
    if (routeState) {
      if (validateLoginState(store, to, from)) {
        next()
      } else {
        next(false)
      }
    } else {
      routeState = true
      // 初始化
      init(store, to, next)
    }
  })

  router.afterEach((to, from) => {
    localStorage.setItem('storage_version', window.VUE_APP_VERSION)
    let elEment = document.getElementsByClassName('m_content')[0]
    elEment && elEment.scrollIntoView()

    // 页面跳转 -- 回到首页，剩余弹窗开始
    const { name = 'menu' } = to
    const { DUALOG_MANAGER_KEY = 'dualog_manager_key' } = store?.state?.dialog || {}
    if (name === 'menu') PublishSubscribe.publish(DUALOG_MANAGER_KEY)
  })

  return router
}

export default createRouter
