/**
 * @desc: 存储弹窗显示数据，用于控制弹窗显示（清除弹窗序列的时候）
 * */
import { make } from 'vuex-pathify'

const state = {
  // 弹窗执行订阅标识
  DUALOG_MANAGER_KEY: 'dualog_manager_key',
  // 弹窗是否正在执行订阅标识
  DUALOG_MANAGER_IS_NEXT: 'dualog_manager_is_next',
  // 弹窗清除订阅标识
  DUALOG_MANAGER_CLEAR: 'dualog_manager_clear',
  /**
   * 用于退出登录，监听被踢下线的时候，防止还有其他弹窗出现的状况 ---> 踢下线的弹窗是（通用弹窗）
   * 【公告弹窗，洗码弹窗，充值成功弹窗，轮播弹窗，vip升级弹窗，vip保级的弹窗，vip是否弹提示可领取弹窗，loading，message，提现成功弹窗，下载弹窗，游戏弹窗】
   */
  popupState: true
}

const getters = {}
const mutations = make.mutations(state)
const actions = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
